.main-content__cards {
  @include vp-1439 {
    margin: 0 auto;
  }
}

.catalog-cards {
  width: fit-content;
}

.catalog-cards .container {
  padding: 0;

  @include vp-1023 {
    padding: 0;
  }
}

.catalog-cards__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 30px;
  margin: 0;
  padding: 25px 0 0;
  list-style: none;

  @include vp-1919 {
    grid-template-columns: repeat(3, minmax(318px, 480px));
    padding: 40px 0 0;
  }

  @include vp-1439 {
    grid-template-columns: repeat(3, minmax(301px, 480px));
    padding: 30px 0 0;
    grid-gap: 20px;
  }

  @include vp-1279 {
    grid-gap: 30px;
  }

  @include vp-1023 {
    grid-template-columns: repeat(2, minmax(335px, 510px));
  }

  @include vp-767 {
    grid-template-columns: repeat(2, minmax(137px, 380px));
    grid-gap: 15px;
    padding-top: 13px;
  }
}

.catalog-card {
  border: 1px solid $color-dark-gray;
  max-width: 350px;
  position: relative;
  padding: 19px 0 0;

  @include vp-1919 {
    max-width: 480px;
    min-height: 629px;
  }

  @include vp-1439 {
    min-height: 606px;
  }

  @include vp-1023 {
    max-width: 510px;
    min-height: 607px;
  }

  @include vp-767 {
    max-width: 380px;
    min-height: 560px;
  }
}

.catalog-card__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.catalog-card__logo {
  position: absolute;
  top: 9px;
  right: 15px;

  @include vp-1439 {
    right: 14px;
  }

  @include vp-1023 {
    right: 9px;
  }

  @include vp-767 {
    left: 3px;
    top: 160px;
  }
}

.catalog-card--lgmg .catalog-card__logo {
  top: 12px;
  width: 93px;
  height: 18px;

  @include vp-1023 {
    right: 10px;
    top: 9px;
  }

  @include vp-767 {
    right: 10px;
    top: 164px;
    width: 60px;
    height: 12px;
  }
}

.catalog-card__label {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 75px;
  max-height: 24px;
  padding: 0 10px;
  position: absolute;
  top: 9px;
  left: -15px;
  font-weight: 700;
  font-size: 12px;
  line-height: 24px;
  text-transform: uppercase;
  border: 2px solid $color-default-black;
  color: $color-default-black;
  background-color: $color-default-white;
  z-index: 1;

  &--sale {
    left: -16px;
    border-color: $color-alizarin-red;
    color: $color-alizarin-red;
  }

  @include vp-1919 {
    &--sale {
      left: -18px;
    }
  }

  @include vp-1439 {
    min-width: 74px;

    &--sale {
      left: -15px;
    }
  }

  @include vp-767 {
    min-width: 61px;
    padding: 0 8px;
    top: -7px;
    left: -10px;
    font-size: 10px;
    line-height: 18px;

    &--sale {
      top: -7px;
      left: -10px;
    }
  }
}

.catalog-card__container {
  padding: 0 11px;

  @include vp-1439 {
    padding: 0 6px;
  }

  @include vp-1023 {
    padding: 0 5px;
  }

  @include vp-767 {
    padding: 0;
  }
}

.catalog-card img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.catalog-card--zoomlion {
  & .catalog-card__logo {
    width: 70px;
    height: 24px;
  }

  & .catalog-card__image {
    width: 320px;
    height: 180px;
    margin: 0 auto 20px;
  }

  @include vp-1919 {
    & .catalog-card__image {
      width: 288px;
    }
  }

  @include vp-1439 {
    & .catalog-card__image {
      width: 271px;
      height: 170px;
      margin-bottom: 16px;
    }
  }

  @include vp-767 {
    & .catalog-card__image {
      width: 127px;
      height: 137px;
      margin-bottom: 34px;
    }

    & .catalog-card__logo {
      width: 58px;
      height: 20px;
    }
  }
}

.catalog-card--genie {
  & .catalog-card__logo {
    width: 64px;
    height: 24px;
  }

  & .catalog-card__image {
    width: 218px;
    height: 195px;
    margin: 0 auto 5px;
  }

  @include vp-1919 {
    & .catalog-card__image {
      width: 197px;
      height: 180px;
      margin: 0 auto 20px;
    }
  }

  @include vp-1439 {
    & .catalog-card__image {
      width: 184px;
      height: 170px;
      margin: 0 auto 16px 64px;
    }
  }

  @include vp-1023 {
    & .catalog-card__image {
      width: 164px;
      height: 150px;
      transform: translate(-18px, 12px);
      margin: 0 auto 36px;
    }
  }

  @include vp-767 {
    & .catalog-card__image {
      width: 122px;
      height: 107px;
      margin-bottom: 65px;
      transform: translate(0, 12px);
    }

    & .catalog-card__logo {
      width: 53px;
      height: 20px;
      z-index: 1;
    }
  }
}

.catalog-card--lgmg .catalog-card__image {
  width: 322px;
  height: 195px;
  margin: 0 15px 5px 13px;

  @include vp-1919 {
    width: 288px;
    height: 180px;
    margin: 0 auto 20px;
  }

  @include vp-1439 {
    width: 271px;
    height: 170px;
    margin-bottom: 16px;
  }

  @include vp-767 {
    width: 127px;
    height: 137px;
    margin-bottom: 34px;
  }
}

.catalog-card__inner {
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @include vp-767 {
    margin-bottom: 17px;
  }
}

.catalog-card__title {
  margin: 0 3px 15px;
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;

  @include vp-1439 {
    font-size: 16px;
    line-height: 22px;
    margin-bottom: 9px;
  }

  @include vp-1023 {
    margin: 0 4px 10px;
  }

  @include vp-767 {
    margin-left: 3px;
    margin-right: 3px;
    margin-bottom: 7px;
    font-size: 12px;
    line-height: 16px;
  }
}

.catalog-card__specs {
  margin: 0 5px 16px;
  padding: 0;
  list-style: none;

  @include vp-1439 {
    margin: 0 5px 12px 6px;
  }

  @include vp-1023 {
    margin: 0 6px 12px 7px;
  }

  @include vp-767 {
    margin: 0 5px 15px;
  }
}

.catalog-card__price {
  display: flex;
  flex-direction: column;

  @include vp-1919 {
    padding: 0 3px;
  }

  @include vp-1439 {
    margin: 0 5px;
  }

  @include vp-1023 {
    margin: 0 3px;
  }
}

.card-specs__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 3px;

  @include vp-767 {
    justify-content: flex-start;
  }

  &:nth-child(2n) {
    background-color: $color-white-smoke;

    @include vp-767 {
      background-color: $color-default-white;
    }
  }

  &:nth-child(2n-1) {
    @include vp-767 {
      background-color: $color-white-smoke;
    }
  }
}

.card-specs__wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.card-specs__icon {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  left: 0;

  &--type {
    width: 19px;
    height: 20px;
  }

  &--height {
    width: 24px;
    height: 19px;
  }

  &--capacity {
    width: 17px;
    height: 19px;
  }

  &--engin {
    width: 24px;
    height: 16px;
  }

  &--weight {
    width: 20px;
    height: 16px;
  }

  @include vp-767 {
    width: 15px;
    height: 16px;
  }
}

.card-specs__item:nth-child(2n -1) svg {
  transform: translate(3px, -50%);

  @include vp-767 {
    transform: translate(0, -50%);
  }
}

.card-specs__title {
  margin-left: 33px;
  font-weight: 400;
  font-size: 12px;
  line-height: 20px;
  transform: translateY(1px);
  color: $color-zambezi;

  @include vp-767 {
    display: none;
  }
}

.card-specs__type {
  font-weight: 700;
  font-size: 12px;
  line-height: 20px;
  color: $color-zambezi;
  margin-left: 15px;

  @include vp-767 {
    margin-left: 23px;
    font-size: 10px;
    line-height: 16px;
  }
}

.card-price__title {
  font-weight: 400;
  font-size: 12px;
  line-height: 20px;

  @include vp-767 {
    font-size: 10px;
    line-height: 12px;
  }
}

.card-price__old-sale {
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  color: $color-dark-gray;

  @include vp-767 {
    font-size: 12px;
    line-height: 15px;
  }
}

.card-price__sale {
  margin: 0;
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
  color: $color-alizarin-red;

  @include vp-767 {
    font-size: 16px;
    line-height: 20px;
  }

  &--text {
    margin-top: 5px;
    margin-left: 7px;
    font-size: 24px;
    line-height: 29px;

    @include vp-767 {
      margin-left: 1px;
      font-size: 14px;
      line-height: 17px;
    }
  }
}

.card-price__sale-container {
  display: flex;
}

.card-price__sale-wrapper {
  position: relative;
  padding-left: 2px;
}

.explanation {
  opacity: 0;
  overflow: hidden;
  position: absolute;
  top: -15px;
  left: 25px;
  width: 180px;
  height: 0;
  margin: 0;
  padding: 0 26px 0 10px;
  z-index: 1;
  background-color: $color-black-transparent4;
  border-radius: 5px;
  color: $color-default-white;
  transition: all $trans-600;
}

@media (hover: hover), screen and (min-width: 0\0) {
  .card-price__sale-icon:hover + .explanation,
  .card-price__sale-icon:focus + .explanation {
    overflow: visible;
    opacity: 1;
    height: fit-content;
  }
}

.explanation__title {
  display: block;
  font-weight: 700;
  font-size: 14px;
  line-height: 20px;
  margin-top: 10px;
  margin-bottom: 3px;
}

.explanation__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.explanation__item {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;

  &:last-child {
    margin-bottom: 15px;
  }
}

.explanation__description {
  margin-bottom: 1px;
}

.catalog-card__comparison {
  margin-top: 4px;
  margin-bottom: 19px;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: $color-zambezi;

  @include vp-1439 {
    margin-bottom: 20px;
  }

  @include vp-767 {
    margin-left: 3px;
    margin-bottom: 13px;
    font-size: 10px;
    line-height: 12px;
  }
}

.catalog-card__comparison svg {
  width: 21px;
  height: 26px;
  margin-top: -6px;
  margin-right: 10px;

  @include vp-767 {
    width: 15px;
    height: 19px;
    margin-right: 5px;
  }
}

.catalog-card__buttons {
  display: flex;
  position: relative;

  @include vp-767 {
    flex-direction: column;
  }
}

.catalog-card__buttons .request-block {
  position: absolute;
  bottom: 100%;
  left: 1px;
  width: calc(100% - 2px);
  border-top: 1px solid $color-dark-gray;
  border-bottom: 1px solid $color-dark-gray;
  box-sizing: border-box;
  background-color: $color-default-white;
  transition: transform $trans-default;
  transform: scaleY(0);
  transform-origin: 0 100%;
}

.btn-reservation--active + .request-block {
  transform: scaleY(1);
}

.request-block__form {
  display: flex;
}

.request-block__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &:first-child {
    width: 125px;
    min-width: 125px;
  }
}

.request-block__wrapper label {
  width: 102px;
  font-weight: 400;
  font-size: 12px;
  line-height: 20px;
  padding: 0 10px;
}

.request-block__wrapper input {
  width: 102px;
  text-align: end;
  padding: 0 10px 4px;
  font-weight: 700;
  font-size: 14px;
  line-height: 24px;
  border: none;
  outline: none;
}

.request-block__wrapper .formula {
  text-align: start;
  width: 100%;
  line-height: 20px;
}

.request-block__button {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 24px;
  height: 24px;
  border: none;
  padding: 0;
  border-left: 1px solid $color-dark-gray;
  border-right: 1px solid $color-dark-gray;
  box-sizing: border-box;
  background-color: $color-default-white;
  color: $color-dark-gray;
}

.request-block__button--plus {
  bottom: auto;
  top: 0;
  border-bottom: 1px solid $color-dark-gray;
}

.request-block__button--plus svg {
  transform: rotate(180deg);
}

.catalog-card__buttons .btn {
  width: 50%;
  margin: 0;
  padding: 10px 20px 9px;
  text-align: center;

  @include vp-1919 {
    padding: 11px 20px 8px;
  }

  @include vp-1439 {
    padding-top: 8px;
  }

  @include vp-767 {
    width: 100%;
  }
}

.catalog-card__buttons .btn-reservation {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $color-default-black;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  transition: all $trans-default;

  &--active {
    width: 55%;
    background-color: $color-alizarin-red;
    padding-left: 10px;
    padding-right: 10px;
  }

  @include vp-1919 {
    font-size: 12px;
    padding-left: 10px;
    padding-right: 10px;
  }

  @include vp-767 {
    font-size: 10px;
  }
}

.catalog-card__buttons .btn--deeper {
  display: flex;
  padding-left: 22px;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 14px;
  line-height: 24px;
  text-transform: uppercase;
  color: $color-default-black;
  background-color: $color-white-smoke;
  transition: color $trans-600;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      color: $color-alizarin-red;
      opacity: 1;
    }
  }

  @include vp-1919 {
    font-size: 12px;
  }

  @include vp-1439 {
    padding-left: 17px;
    padding-right: 17px;
  }

  @include vp-1023 {
    padding-left: 18px;
    padding-right: 16px;
  }

  @include vp-767 {
    font-size: 10px;
    padding: 3px 20px;
  }
}

.btn--deeper svg {
  margin-top: -1px;
  margin-left: 6px;
  width: 30px;
  height: 14px;

  @include vp-1023 {
    margin-left: 4px;
  }
}

.btn-reservation--active ~ .btn--deeper {
  width: 45%;
  padding-left: 10px;
  padding-right: 10px;

  @include vp-1919 {
    padding-left: 5px;
    padding-right: 5px;
  }
}
