.files {
  margin: 0;
  padding: 0;

  display: grid;
  grid-template-columns: repeat(2, 1fr);

  list-style: none;

  @include vp-639 {
    grid-template-columns: 1fr;
  }

  &__item {
    box-shadow: 0 2px $color-whisper;
  }

  &__item:nth-child(odd) {
    box-shadow: 2px 2px $color-whisper;
  }

  &__link {
    width: 100%;
    height: 100%;
    padding: 30px 58px 30px 60px;

    display: grid;
    grid-template-columns: minmax(320px, 420px) 1fr;
    column-gap: 30px;

    cursor: pointer;
    transition: background-color $trans-600;

    @include vp-1439 {
      padding: 30px;
    }

    @include vp-1279 {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    @include vp-767 {
      padding: 25px 15px;
    }

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        background-color: $color-white-smoke;

        .files__title,
        .files__button-note span:first-child {
          color: $color-alizarin-red;
        }

        .files__img::after {
          background-color: rgba($color-alizarin-red, 0.3);
        }
      }
    }
  }

  &__img {
    position: relative;
    max-width: 420px;
    height: max-content;

    @include vp-1279 {
      width: 100%;
      max-width: 100%;
      margin-bottom: 20px;
    }

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;

      width: 100%;
      height: 100%;

      background-color: transparent;
      transition: background-color $trans-600;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;

    @include vp-1279 {
      width: 100%;

      flex-direction: row;
      justify-content: space-between;
    }

    @include vp-1023 {
      flex-wrap: wrap;
      justify-content: flex-end;
    }
  }

  &__title {
    margin-bottom: 20px;

    transition: color $trans-600;

    @include vp-1023 {
      margin: 0 15px 15px 0;
    }

    @include vp-767 {
      margin: 0 0 15px 0;
    }
  }

  &__button {
    position: relative;
    z-index: 20;

    display: flex;
    align-items: center;

    background-color: transparent;
    border: none;
    cursor: pointer;
    transition: opacity $trans-600;

    svg {
      width: 32px;
      height: 33px;
      margin-right: 11px;

      color: $color-alizarin-red;

      @include vp-1023 {
        width: 22px;
        height: 23px;
      }
    }
  }

  &__button-note {
    margin: 0;
    padding: 0;

    display: flex;
    flex-direction: column;
    align-items: flex-start;

    @include vp-767 {
      flex-direction: row;
    }
  }

  &__button-name {
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: $color-default-black;
    text-transform: uppercase;

    transition: color $trans-600;

    @include vp-1279 {
      font-size: 16px;
      line-height: 20px;
    }

    @include vp-767 {
      margin-right: 15px;

      font-size: 14px;
      line-height: 18px;
    }
  }

  &__button-size {
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: $color-dark-gray;

    @include vp-1279 {
      font-size: 14px;
      line-height: 18px;
    }

    @include vp-767 {
      font-size: 12px;
      line-height: 16px;
    }
  }
}
