.contacts-list {
  margin: 0;
  padding: 0;

  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: auto;

  column-gap: 15px;
  row-gap: 43px;
  list-style: none;

  @include vp-1279 {
    grid-template-columns: 1fr;
    row-gap: 20px;
  }

  &__item {
    padding: 0 5px;

    display: grid;
    grid-template-columns: auto 1fr;

    svg {
      width: 30px;
      height: 30px;

      color: $color-alizarin-red;

      @include vp-1279 {
        width: 25px;
        height: 25px;
      }
    }
  }

  &__label {
    margin-left: 15px;
    display: flex;
    flex-direction: column;
  }

  &__note {
    font-size: 18px;
    line-height: 22px;
    color: $color-dark-gray;

    @include vp-1279 {
      margin-bottom: 5px;

      font-size: 16px;
      line-height: 18px;
    }

    @include vp-767 {
      font-size: 14px;
      line-height: 16px;
    }
  }

  &__link {
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;

    color: $color-default-black;
    transition: color $trans-600;

    @include vp-1439 {
      font-size: 22px;
      line-height: 28px;
    }

    @include vp-1279 {
      font-size: 20px;
      line-height: 24px;
    }

    @include vp-1023 {
      font-size: 18px;
      line-height: 20px;
    }

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        color: $color-alizarin-red;
        transition: color $trans-600;
      }
    }
  }

  &__text {
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    font-style: normal;

    @include vp-1439 {
      font-size: 16px;
      line-height: 18px;
    }

    @include vp-1023 {
      font-size: 14px;
      line-height: 16px;
    }
  }
}
