.form-field {
  position: relative;
  display: flex;

  input {
    width: 100%;
    padding: 10px 15px;

    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
    color: $color-vulcan;

    border: 1px solid $color-whisper;
    transition: opacity $trans-600, background-color $trans-600, border-color $trans-600;

    @include vp-1023 {
      font-size: 14px;
      line-height: 14px;
    }

    &::placeholder {
      font-weight: 500;
      font-size: 16px;
      line-height: 16px;
      color: $color-dark-gray;
    }

    &:hover {
      cursor: pointer;
      background-color: $color-white-smoke;
      opacity: 0.8;
      transition: opacity $trans-600, background-color $trans-600;
    }

    &:focus {
      border-color: $color-vulcan;
      outline: 0;
      transition: border-color $trans-600;
    }
  }
}

.is-invalid {
  input,
  textarea {
    border-color: $color-alizarin-red;
  }
}
