.product-slider {
  position: relative;

  max-width: 733px;
  height: 590px;
  margin-bottom: 10px;
  padding: 15px 20px;

  border: 1px solid $color-dark-gray;

  @include vp-1919 {
    max-width: 589px;
    height: 560px;
    margin-bottom: 0;
  }

  @include vp-1439 {
    max-width: 540px;
  }

  @include vp-1279 {
    max-width: 589px;
  }

  @include vp-1023 {
    max-width: 371px;
    height: 395px;
    padding: 10px;
  }

  @include vp-767 {
    max-width: 100%;
    min-width: 280px;
    width: 100%;
    height: auto;
    margin-bottom: 32px;
  }
}

.product-slider__label {
  position: absolute;
  z-index: 10;
  top: 20px;
  left: -15px;

  @include vp-1919 {
    top: 15px;
  }

  @include vp-1023 {
    top: 10px;
    left: -12px;
  }
}

.product-slider__main {
  position: relative;
  width: 100%;
  height: 100%;
  margin-bottom: 34px;

  @include vp-1919 {
    margin-bottom: 18px;
  }

  @include vp-767 {
    margin-bottom: 10px;
    padding-bottom: 40px;
  }
}

.product-slider__logo {
  position: absolute;
  z-index: 20;
  top: 0;
  right: 0;

  width: 140px;
  height: 48px;
  object-fit: contain;

  @include vp-1023 {
    width: 88px;
    height: 30px;
  }

  @include vp-767 {
    top: unset;
    right: unset;
    bottom: 0;
    left: 0;
  }
}

.product-slider__thumbs-wrapper {
  position: relative;
  margin: 0 25px;

  @include vp-1919 {
    margin: 0 18px;
  }

  @include vp-1439 {
    margin: 0 9px;
  }

  @include vp-1279 {
    margin: 0 15px;
  }

  @include vp-767 {
    margin: 0 10px;
  }
}

.product-slider__main-list,
.product-slider__thumbs-list,
.product-slider__modal-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.product-slider__main-item {
  position: relative;
  width: 693px;
  height: 400px;

  transition: opacity $trans-600;

  @include vp-1279 {
    width: 549px;
    height: 400px;
  }

  @include vp-1023 {
    width: 351px;
    height: 270px;
  }

  @include vp-767 {
    width: 280px;
    height: auto;
    min-height: 220px;
  }

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: contain;

    @include vp-1919 {
      object-fit: cover;
    }
  }

  &:hover {
    opacity: 0.8;
    cursor: pointer;
  }

  svg {
    position: absolute;
    top: 0;
    right: 0;

    z-index: 20;


    width: 140px;
    height: 48px;

    cursor: default;

    @include vp-1023 {
      width: 88px;
      height: 30px;
    }

    @include vp-767 {
      top: unset;
      right: unset;
      bottom: -40px;
      left: 0;
    }
  }
}

.product-slider__thumbs-item {
  position: relative;

  min-width: 120px;
  max-width: 120px;
  min-height: 110px;
  max-height: 110px;

  border: 1px solid $color-dark-gray;

  transition: opacity $trans-600;


  @include vp-1023 {
    min-width: 99px;
    max-width: 99px;
    min-height: 90px;
    max-height: 90px;
  }

  @media (max-width: 320px) {
    min-width: 75px;
    max-width: 75px;
    min-height: 68px;
    max-height: 68px;
  }

  &--active {
    border: 3px solid $color-alizarin-red;
  }

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &:hover {
    opacity: 0.7;
    cursor: pointer;
  }
}

.product-slider__button {
  position: absolute;
  z-index: 1;
  bottom: 20px;
  top: 46%;
  transform: translateY(-50%);

  margin: 0;
  padding: 0;

  display: flex;
  justify-content: center;
  align-items: center;

  color: $color-alizarin-red;
  background-color: transparent;
  border: 0;

  @include vp-1919 {
    top: 52%;
  }

  svg {
    width: 30px;
    height: 28px;

    @include vp-1919 {
      width: 16px;
      height: 18px;
    }
  }

  &--next {
    right: -35px;

    @include vp-1023 {
      right: -28px;
    }

    @include vp-767 {
      right: -23px;
    }
  }

  &--prev {
    left: -35px;

    @include vp-1023 {
      left: -28px;
    }

    @include vp-767 {
      left: -23px;
    }
  }

  &::after {
    display: none;
  }

  &:hover {
    cursor: pointer;
    opacity: 0.8;
  }

  &--disabled {
    opacity: 0.6;

    &:hover {
      cursor: default;
      opacity: 0.6;
    }
  }
}

// Modal

.product-slider__button-modal {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);

  width: 64px;
  height: 60px;
  padding: 0;

  background-color: transparent;
  border: none;
  cursor: pointer;

  @include vp-1023 {
    width: 54px;
    height: 50px;
  }

  @include vp-767 {
    width: 34px;
    height: 30px;
  }

  svg {
    width: 64px;
    height: 60px;

    color: $color-default-white;

    @include vp-1023 {
      width: 54px;
      height: 50px;
    }

    @include vp-767 {
      width: 34px;
      height: 30px;
    }
  }

  &--next {
    right: -50px;

    @include vp-767 {
      right: -30px;
    }
  }

  &--prev {
    left: -50px;

    @include vp-767 {
      left: -30px;
    }
  }
}

.product-slider__modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;

  width: 100vw;
  height: 100vh;

  display: none;
  background-color: rgba($color-default-black, 0.4);

  &--active {
    display: block;
  }
}

.product-slider__modal-wrapper {
  position: absolute;
  z-index: 3;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  max-width: calc(970 / 1920 * 100%);
  height: auto;

  @media (max-width: 1700px) {
    max-width: 65%;
  }

  @include vp-1279 {
    max-width: 80%;
  }

  img {
    width: 100%;
    height: 100%;
  }
}

.product-slider__modal-button-close {
  position: absolute;
  z-index: 5;
  top: 10px;
  right: 10px;

  background-color: transparent;
  border: none;
  cursor: pointer;

  transition: opacity $trans-default;

  svg {
    width: 20px;
    height: 20px;

    @include vp-767 {
      width: 10px;
      height: 10px;
    }
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      opacity: 0.5;
    }
  }
}
