.commodity {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template-areas: "slider card"
    "slider info"
    "tabs info"
    "tabs info";

  @include vp-1919 {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
  }

  @include vp-1279 {
    padding: 15px 0 0;
  }

  @include vp-767 {
    justify-content: center;
  }

  .product-slider {
    margin: 0 30px 35px 0;
    grid-area: slider;

    @include vp-1919 {
      margin: 0 30px 30px 0;
    }

    @include vp-1279 {
      margin: 0 30px 28px 0;
    }

    @include vp-767 {
      margin: 0 0 35px 0;
    }
  }

  &__characteristics {
    margin-bottom: 30px;
    grid-area: card;
  }

  &__tabs {
    grid-area: tabs;

    @include vp-1919 {
      flex-basis: 100%;
      flex-grow: 1;
    }
  }

  &__wrap {
    grid-area: info;

    @include vp-1919 {
      margin: 0 0 25px;

      display: grid;
      grid-template-columns: auto 1fr;
      column-gap: 30px;
    }

    @include vp-1279 {
      margin: 0 0 25px;
    }

    @include vp-1023 {
      margin: 0 0 10px;
    }

    @include vp-767 {
      margin: 0 0 23px;

      display: flex;
      flex-direction: column;
    }
  }

  &__documents {
    margin: 0 0 30px;

    @include vp-1919 {
      margin: 0;
    }

    @include vp-767 {
      margin: 0 0 30px;
    }
  }
}
