.region-select {
  &--header {
    @include vp-1919 {
      display: none;
    }
  }

  &--mobile {
    margin-left: 10px;
  }
}

.region-select__text {
  display: block;
  margin: 0;
  font-size: 12px;
  line-height: 14px;
  color: $color-aluminum;
}

.region-select--mobile .region-select__text {
  font-size: 10px;
  font-weight: 400;
}

.region-select--footer .region-select__text {
  font-size: 14px;
  line-height: 17px;
  font-weight: 400;
}

.region-select__city-name {
  position: relative;
  display: block;
  font-size: 12px;
  line-height: 15px;
  font-weight: 600;
  color: $color-default-white;
  text-decoration: underline;
  text-decoration-style: dashed;
  text-decoration-color: $color-alizarin-red;

  &::after {
    content: "";
    position: absolute;
    top: 5px;
    right: 10px;
    display: block;
    width: 12px;
    height: 7px;
    background: url("../img/svg/caret.svg") no-repeat right;
  }
}

.region-select--mobile .region-select__city-name {
  color: $color-default-black;

  &::after {
    right: 0;
  }
}

.region-select--footer .region-select__city-name {
  font-size: 18px;
  line-height: 24px;
  font-weight: 600;
  text-decoration: none;

  &::after {
    top: 10px;
    right: 0;

    @include vp-1919 {
      right: 5px;
    }

    @include vp-1023 {
      right: 16px;
    }
  }

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    display: block;
    width: 75px;
    height: 3px;
    background-color: transparent;
    border-bottom: 3px dashed $color-alizarin-red;

    @include vp-1919 {
      width: 65px;
    }

    @include vp-1023 {
      width: 57px;
    }
  }

  @include vp-1919 {
    font-size: 16px;
  }

  @include vp-1023 {
    font-size: 14px;
  }
}
