.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 20px;

  @include vp-1919 {
    padding: 0 30px;
  }

  @include vp-1023 {
    padding: 0 24px;
  }

  @include vp-767 {
    padding: 0 15px;
  }

  &--no-padding {
    padding: 0;

    @include vp-1919 {
      padding: 0;
    }

    @include vp-1023 {
      padding: 0;
    }

    @include vp-767 {
      padding: 0;
    }
  }

  &--content {
    padding: 0 60px;

    @include vp-1919 {
      padding: 0 60px;
    }

    @include vp-1439 {
      padding: 0 30px;
    }

    @include vp-1023 {
      padding: 0 30px;
    }
  }

  &--unlimited-width {
    max-width: 100%;
    padding: 0;

    @include vp-1919 {
      max-width: 100%;
      padding: 0;
    }

    @include vp-1023 {
      max-width: 100%;
      padding: 0;
    }

    @include vp-767 {
      max-width: 100%;
      padding: 0;
    }
  }
}
