.map {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 20px 0 60px;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: calc(50% - 50vw);
    width: 100vw;
    height: 100%;
    background: linear-gradient(266.2deg, rgba(0, 0, 0, 0.72) 3.38%, rgba(0, 0, 0, 0.9) 96.89%);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }


  img {
    // position: relative;
    transform: translateZ(0);
    width: 100%;
    height: auto;
    object-position: center;
  }
}
