.logo {
  display: block;
  line-height: 0;
}

.logo img {
  @include vp-1919 {
    width: 140px;
    height: 17px;
  }

  @include vp-1439 {
    width: 120px;
    height: 15px;
  }
}

.logo--footer img {
  @include vp-1439 {
    width: 140px;
    height: 17px;
  }

  @include vp-1023 {
    width: 120px;
    height: 15px;
  }
}
