.documents {
  margin: 0;
  padding: 0;

  display: grid;
  grid-template-columns: repeat(2, 1fr);
  list-style: none;

  @include vp-767 {
    grid-template-columns: 1fr;
  }

  &__item {
    padding: 30px 60px 40px;

    box-shadow: 0 2px $color-whisper;

    @include vp-1439 {
      padding: 30px;
    }

    @include vp-767 {
      padding: 25px 15px;
    }
  }

  &__item:nth-child(odd) {
    box-shadow: 2px 2px $color-whisper;
  }

  &__title {
    margin: 0 0 20px;

    font-size: 32px;
    line-height: 40px;

    @include vp-1279 {
      font-size: 24px;
      line-height: 26px;
    }

    @include vp-1023 {
      font-size: 20px;
      line-height: 22px;
    }

    @include vp-767 {
      font-size: 18px;
      line-height: 20px;
    }
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__sub-item:not(:last-child) {
    margin-bottom: 18px;
  }

  &__link {
    max-width: 350px;

    display: grid;
    grid-template-columns: auto 1fr;
    grid-template-areas: "icon ."
      "icon note";
    align-items: center;
    column-gap: 13px;

    font-family: $ff-montserrat;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: $color-default-black;

    transition: color $trans-600;

    @include vp-1023 {
      font-size: 14px;
      line-height: 14px;
    }

    svg {
      grid-area: icon;
      width: 27px;
      height: 36px;
    }

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        color: $color-alizarin-red;
        transition: color $trans-600;

        .documents__note span:first-of-type {
          color: $color-alizarin-red;
        }
      }
    }
  }

  &__note {
    grid-area: note;
    margin: 5px 0 0;

    svg {
      width: 14px;
      height: 14px;
      margin-right: 5px;
      color: $color-alizarin-red;
    }
  }

  &__note-name {
    margin-right: 20px;

    font-weight: 700;
    font-size: 12px;
    line-height: 16px;

    transition: color $trans-600;
  }

  &__note-size {
    font-weight: 700;
    font-size: 12px;
    line-height: 16px;
    color: $color-dark-gray;
  }
}
