.product-specs {
  &__item {
    margin: 0;
    padding: 5px 10px;

    display: grid;
    grid-template-columns: 28px 2fr 1fr;
    column-gap: 10px;

    align-items: center;

    svg {
      width: 28px;
      height: 23px;
    }

    &:nth-child(even) {
      background-color: $color-white-smoke;
    }

    @include vp-1919 {
      padding: 5px 15px;
    }

    @include vp-1279 {
      padding: 5px 3px;
      grid-template-columns: 25px 2fr 1fr;

      svg {
        width: 25px;
        height: 20px;
      }
    }

    @include vp-767 {
      padding: 4px 6px;
    }
  }

  &__text {
    font-size: 14px;
    line-height: 24px;
    color: $color-zambezi;

    @include vp-1279 {
      font-size: 12px;
      line-height: 20px;
    }

    &--value {
      margin-left: auto;
      font-weight: 700;
    }
  }
}
