.login {
  display: flex;
  justify-content: center;
  align-items: center;

  &--header {
    @include vp-1919 {
      display: none;
    }
  }

  &--mobile {
    min-height: 29px;
    margin-left: 10px;
  }
}

.login__text {
  margin: 0;
  font-size: 12px;
  line-height: 15px;
  font-weight: 600;
  color: $color-default-white;
}

.login--mobile .login__text {
  color: $color-default-black;
}
