.main-content__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(4, 479px);
  justify-content: center;
  border-top: 1px solid $color-whisper;

  @include vp-1919 {
    grid-template-columns: repeat(3, 479px);
  }

  @include vp-1439 {
    grid-template-columns: repeat(3, 341px);
  }

  @include vp-1023 {
    grid-template-columns: repeat(2, 384px);
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.container--unlimited-width .card-item {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: fit-content;
  max-width: 440px;
  padding: 19px 20px 21px;
  box-sizing: content-box;

  &:not(:nth-child(4n)) {
    border-right: 1px solid $color-whisper;
  }

  &:nth-child(4n) {
    padding-left: 21px;
  }

  &:nth-child(4n + 2)::after {
    position: absolute;
    bottom: 0;
    left: 0;
    content: "";
    width: 100vw;
    height: 1px;
    background-color: $color-whisper;
  }

  &:nth-child(4n + 1) {
    padding-left: 19px;

    &::after {
      position: absolute;
      bottom: 0;
      right: 0;
      content: "";
      width: 100vw;
      height: 1px;
      background-color: $color-whisper;
    }
  }

  @include vp-1919 {
    padding-bottom: 20px;

    &:not(:nth-child(4n)) {
      border-right: none;
    }

    &:not(:nth-child(3n)) {
      border-right: 1px solid $color-whisper;
    }

    &:nth-child(7n)::after {
      position: absolute;
      bottom: 0;
      right: 0;
      content: "";
      width: 100vw;
      height: 1px;
      background-color: $color-whisper;
    }

    &:nth-child(8n)::after {
      position: absolute;
      bottom: 0;
      left: 0;
      content: "";
      width: 100vw;
      height: 1px;
      background-color: $color-whisper;
    }
  }

  @include vp-1439 {
    padding: 14px 15px 15px;

    &:nth-child(4n) {
      padding-left: 15px;
    }

    &:nth-child(4n + 1) {
      padding-left: 15px;
    }
  }

  @include vp-1023 {
    &:not(:nth-child(3n)) {
      border-right: none;
    }

    &:not(:nth-child(2n)) {
      border-right: 1px solid $color-whisper;
    }

    &:nth-child(3n)::after {
      position: absolute;
      bottom: 0;
      right: 0;
      content: "";
      width: 100vw;
      height: 1px;
      background-color: $color-whisper;
    }

    &:nth-child(4n)::after {
      position: absolute;
      bottom: 0;
      left: 0;
      content: "";
      width: 100vw;
      height: 1px;
      background-color: $color-whisper;
    }
  }

  @include vp-767 {
    max-width: 100%;
    width: 100%;
    padding: 14px 14px 14px;
    border-bottom: 1px solid $color-whisper;

    &--8 {
      padding-bottom: 12px;
    }

    &:not(:nth-child(4n)) {
      border-right: none;
    }

    &::after {
      display: none;
    }

    &:nth-child(4n) {
      padding-left: 14px;
    }

    &:nth-child(4n + 1) {
      padding-left: 14px;
    }
  }
}

.card-item .card-item__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;

  @include vp-767 {
    max-width: 290px;
    align-self: center;
  }
}

.card-item__image-link {
  display: block;
  position: relative;
  margin-bottom: 21px;

  @include vp-1919 {
    margin-bottom: 20px;
  }

  @include vp-1439 {
    margin-bottom: 15px;
  }
}

.card-item__image-link .img-wrapper {
  width: 439px;
  height: 319px;
  background-color: $color-zambezi;

  &::before {
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    width: 439px;
    height: 319px;
    background: $black-to-right2;
    z-index: 1;
    transition: opacity $trans-600;
  }

  @include vp-1439 {
    width: 311px;
    height: 220px;

    &::before {
      width: 311px;
      height: 220px;
    }
  }

  @include vp-1023 {
    width: 354px;
    height: 250px;

    &::before {
      width: 354px;
      height: 250px;
    }
  }

  @include vp-767 {
    width: 290px;
    height: 210px;

    &::before {
      width: 290px;
      height: 210px;
    }
  }
}

@media (hover: hover), screen and (min-width: 0\0) {
  .card-item__image-link:hover .img-wrapper::before,
  .card-item__image-link:focus .img-wrapper::before {
    opacity: 0;
  }
}

.card-item__image-link img {
  position: relative;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card-item__title-wrapper {
  position: absolute;
  bottom: 22px;
  left: 35px;
  z-index: 2;

  @include vp-1919 {
    bottom: 23px;
  }

  @include vp-1439 {
    left: 24px;
    bottom: 17px;
  }

  @include vp-1023 {
    left: 25px;
  }
}

.card-item__title {
  padding: 5px 0;
  text-transform: uppercase;
  color: $color-default-white;
  font-weight: 700;
  font-size: 20px;
  background-color: $color-alizarin-red;
  box-shadow: -15px 0 0 $color-alizarin-red, 15px 0 0 $color-alizarin-red;

  @include vp-1439 {
    padding-top: 6px;
    font-size: 16px;
    line-height: 20px;
    box-shadow: -10px 0 0 $color-alizarin-red, 10px 0 0 $color-alizarin-red;
  }

  @include vp-1023 {
    box-shadow: -10px 0 0 $color-alizarin-red, 11px 0 0 $color-alizarin-red;
  }

  @include vp-767 {
    padding-top: 5px;
    font-size: 14px;
    line-height: 18px;
  }
}

.card-item__wrapper {
  margin: 0 0 31px;
}

.card-sublist {
  columns: 2;
  column-gap: 30px;
  margin: 0 0 31px;
  padding: 0;
  list-style: none;
  margin-left: -1px;

  @include vp-1439 {
    columns: 1;
    column-gap: 0;
    margin-left: -1px;
    margin-bottom: 10px;
  }

  @include vp-767 {
    margin-bottom: 21px;
  }
}

.card-item--6 .card-sublist,
.card-item--7 .card-sublist {
  columns: 1;

  @include vp-767 {
    margin-bottom: 21px;
  }
}

.card-item--3 .card-sublist {
  @include vp-767 {
    margin-bottom: 8px;
  }
}

.sublist-item {
  break-inside: avoid;
  max-width: 205px;

  @include vp-1439 {
    min-width: 100%;
  }
}

.sublist-item:not(:first-child) {
  margin-top: 8px;
}

.sublist-item__link {
  width: fit-content;
  font-size: 14px;
  line-height: 18px;
  text-decoration: underline;
  display: flex;
  align-items: flex-start;
  outline: none;
  transition: color $trans-600;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      color: $color-alizarin-red;
    }
  }
}

.sublist-item__icon {
  color: $color-alizarin-red;
  min-width: 7px;
  width: 7px;
  height: 12px;
  margin-top: 3px;
  margin-right: 9px;
  display: block;
}

.card-item__link {
  width: max-content;
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  outline: none;
  transition: color $trans-600;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      color: $color-alizarin-red;
    }
  }
}

.card-item__link svg {
  margin-left: 7px;

  @include vp-1439 {
    margin-left: 6px;
    width: 36px;
    height: 16px;
  }
}
