.title {
  width: 100%;
  margin: 0;

  font-weight: 800;
  font-family: $ff-ttautonomous;
  text-transform: uppercase;

  &--h1 {
    font-size: 50px;
    line-height: 60px;

    @include vp-1919 {
      font-size: 40px;
      line-height: 55px;
    }

    @include vp-1279 {
      font-size: 30px;
      line-height: 40px;
    }

    @include vp-767 {
      font-size: 24px;
      line-height: 32px;
    }
  }

  &--h2 {
    font-size: 24px;
    line-height: 30px;

    @include vp-1919 {
      font-size: 20px;
      line-height: 24px;
    }

    @include vp-1279 {
      font-size: 16px;
      line-height: 20px;
    }

    @include vp-767 {
      font-size: 14px;
      line-height: 18px;
    }
  }

  &--h3 {
    font-size: 70px;
    line-height: 90px;

    @include vp-1919 {
      font-size: 50px;
      line-height: 70px;
    }

    @include vp-1279 {
      font-size: 40px;
      line-height: 55px;
    }

    @include vp-767 {
      font-size: 24px;
      line-height: 32px;
    }
  }
}
