.form__field {
  margin-bottom: 20px;

  @include vp-767 {
    margin-bottom: 15px;
  }

  &--margin-small {
    margin-bottom: 15px;
  }

  &--checkbox {
    margin-bottom: 12px;

    @include vp-767 {
      margin-bottom: 12px;
    }
  }
}

.form__warning {
  position: absolute;
  bottom: -14px;
  left: 0;

  font-weight: 600;
  font-size: 12px;
  line-height: 14px;

  opacity: 0;
  color: $color-alizarin-red;
  transition: opacity $trans-600;

  @include vp-767 {
    font-size: 11px;
    line-height: 13px;
  }
}

.is-invalid {
  .form__warning {
    opacity: 1;
  }
}
