.content {
  h1,
  h2,
  h3 {
    width: 100%;
    margin: 0;

    font-weight: 800;
    font-family: $ff-ttautonomous;
    text-transform: uppercase;
  }

  h1:first-child,
  h2:first-child,
  h3:first-child {
    margin-top: 0;
  }

  h1 {
    margin-top: 50px;

    font-size: 50px;
    line-height: 60px;
    font-weight: 700px;

    @include vp-1919 {
      font-size: 40px;
      line-height: 55px;
    }

    @include vp-1279 {
      font-size: 30px;
      line-height: 40px;
    }

    @include vp-767 {
      font-size: 24px;
      line-height: 32px;
    }
  }

  h2 {
    margin-top: 40px;
    margin-bottom: 20px;

    font-size: 32px;
    line-height: 40px;
    font-weight: 700px;

    @include vp-1919 {
      font-size: 30px;
      line-height: 38px;
    }

    @include vp-1279 {
      font-size: 28px;
      line-height: 34px;
    }

    @include vp-767 {
      margin-bottom: 16px;

      font-size: 20px;
      line-height: 22px;
    }
  }

  h3 {
    margin-top: 30px;
    margin-bottom: 15px;

    font-size: 24px;
    line-height: 32px;
    font-weight: 700px;

    @include vp-767 {
      margin-bottom: 12px;

      font-size: 18px;
      line-height: 22px;
    }
  }

  p.accent {
    margin-top: 0;
    margin-bottom: 15px;

    font-size: 24px;
    line-height: 32px;
    font-style: normal;

    @include vp-767 {
      margin-bottom: 10px;

      font-size: 20px;
      line-height: 24px;
    }
  }

  p {
    margin-top: 0;
    margin-bottom: 15px;

    font-size: 16px;
    line-height: 24px;

    @include vp-767 {
      margin-bottom: 12px;

      line-height: 20px;
    }
  }

  .gallery-slider {
    margin: 30px 0;
  }
}
