.brands {
  margin-bottom: 100px;
  overflow: hidden;

  border-top: $border-grey;
  border-bottom: $border-grey;

  @include vp-1919 {
    margin-bottom: 58px;

    border-top: none;
    border-bottom: none;
  }

  @include vp-1279 {
    margin-bottom: 66px;
  }

  @include vp-1023 {
    margin-bottom: 52px;
  }

  .container {
    display: flex;

    @include vp-1919 {
      flex-direction: column;
    }
  }

  .title--h1 {
    width: 100%;
    max-width: 450px;
    padding-top: 60px;

    text-align: center;

    @include vp-1919 {
      margin-bottom: 65px;
      margin-left: 60px;
      padding-top: 0;

      text-align: left;

      border-top: none;
    }

    @include vp-1279 {
      margin-bottom: 27px;
      margin-left: 30px;
    }

    @include vp-767 {
      margin-bottom: 15px;
      margin-left: 15px;
    }
  }

  &__list {
    display: grid;
    margin: 0;
    padding: 0;
    padding-bottom: 2px;

    list-style: none;

    border-left: $border-grey;

    grid-template-columns: repeat(4, 25%);

    @include vp-1919 {
      border-top: $border-grey;
      border-bottom: $border-grey;
      border-left: none;
    }

    @include vp-1279 {
      grid-template-columns: repeat(3, 33.33%);
    }

    @include vp-767 {
      grid-template-columns: repeat(2, 50%);
    }

    @include vp-639 {
      grid-template-columns: repeat(1, 100%);
    }
  }

  .brands-card {
    position: relative;

    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 368px;
    min-height: 380px;
    margin-bottom: -4px;
    padding: 20px;

    text-align: center;

    border-right: $border-grey;
    border-bottom: $border-grey;

    @include vp-1439 {
      padding: 17px;
    }

    @include vp-1023 {
      padding: 17px 15px;
    }

    @include vp-1919 {
      max-width: 100%;
    }

    @include vp-1279 {
      min-height: 325px;
    }

    @include vp-1023 {
      min-height: 300px;
    }

    @include vp-639 {
      border: none;
      border-bottom: $border-grey;
    }

    &:nth-child(2n) {
      @include vp-767 {
        border-right-color: $color-transparent;
      }
    }

    &:nth-child(3n) {
      @include vp-1279 {
        border-right-color: $color-transparent;
      }

      @include vp-767 {
        border-right-color: $color-whisper;
      }
    }

    &:nth-child(4n) {
      border-right-color: $color-transparent;

      @include vp-1279 {
        border-right-color: $color-whisper;
      }
    }

    &__img {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 80px;
      margin-top: 30px;
      margin-bottom: 25px;

      @include vp-1919 {
        margin-bottom: 20px;
      }

      @include vp-1279 {
        width: 174px;
        height: 60px;
        margin-top: 20px;
        margin-bottom: 15px;
      }

      @include vp-1023 {
        width: 140px;
        height: 48px;
        margin-top: 15px;
        margin-bottom: 18px;
      }

      img {
        margin: 0 auto;

        object-fit: contain;
      }
    }

    &__description {
      margin: 0 auto 37px;

      font-size: 16px;
      line-height: 20px;
      color: $color-zambezi;

      @include vp-1919 {
        margin-bottom: 50px;

        font-size: 14px;
        line-height: 18px;
      }

      @include vp-1279 {
        margin-bottom: 40px;
      }

      @include vp-1023 {
        margin-bottom: 20px;
      }

      @include vp-767 {
        margin-bottom: 40px;
      }
    }

    .btn {
      margin: 0;
      padding: 10px 20px;

      font-weight: 700;
      font-size: 14px;
      line-height: 16px;
      color: $color-default-black;

      border: 2px solid $color-default-black;

      transition: all  $trans-600;

      @include vp-1023 {
        padding: 10px;
      }

      @include vp-767 {
        padding: 10px 20px;
      }

      @media (hover: hover), screen and (min-width: 0\0) {
        &:hover,
        &:focus {
          color: $color-default-white;

          background-color: $color-alizarin-red;
          border-color: $color-alizarin-red;
        }
      }
    }

    .link {
      margin-top: 17px;

      @include vp-1919 {
        margin-top: 14px;
      }

      @include vp-1279 {
        margin-top: 22px;
      }

      @include vp-1023 {
        margin-top: 14px;
      }

      @include vp-767 {
        margin-top: 20px;
      }
    }
  }
}
