.grid {
  display: grid;
  grid-column-gap: 30px;
  grid-row-gap: 30px;

  @include vp-1279 {
    grid-column-gap: 20px;
    grid-row-gap: 20px;
  }

  &--col-2 {
    grid-template-columns: 1fr 1fr;

    @include vp-1023 {
      grid-template-columns: 100%;
    }
  }

  &--col-4 {
    grid-template-columns: 1fr 1fr 1fr 1fr;

    @include vp-1279 {
      grid-template-columns: 1fr 1fr 1fr;
    }

    @include vp-1023 {
      grid-template-columns: 1fr 1fr;
    }

    @include vp-767 {
      grid-template-columns: 1fr 1fr;
    }
  }

  &--gap-60 {
    grid-column-gap: 60px;
    grid-row-gap: 60px;

    @include vp-1023 {
      grid-column-gap: 40px;
      grid-row-gap: 40px;
    }
  }
}

// стили для карточек можно перенести в вид модификаторов .card-news--big .card-news--wide
.grid--news {
  .card-news:first-child {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 3;

    @include vp-1439 {
      grid-column-start: 1;
      grid-column-end: 5;
      grid-row-start: 1;
      grid-row-end: 3;

      min-height: 475px;

      @include vp-1279 {
        grid-column-start: 1;
        grid-column-end: 4;
      }

      @include vp-1023 {
        grid-column-start: 1;
        grid-column-end: 3;
      }

      @include vp-767 {
        min-height: 425px;
      }
    }

    position: relative;

    .card-news__img {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;

      width: 100%;
      height: 100%;

      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;

        width: 100%;
        height: 100%;
        pointer-events: none;

        background: rgba($color: #000000, $alpha: 0.3);
        background-image: url('../img/content/card-pattern.png');
        background-repeat: repeat;
      }
    }

    .card-news__inner {
      margin-top: auto;
      z-index: 2;
      flex-grow: 0;

      padding: 30px;

      @include vp-767 {
        padding: 14px 14px 20px;
      }
    }

    .card-news__info {
      position: absolute;
      top: 30px;
      right: 30px;
      color: $color-default-white;

      @include vp-767 {
        top: 14px;
        right: 14px;
      }

      svg {
        color: $color-alizarin-red;

        width: 17px;
        height: 24px;
      }

      font-size: 18px;
      line-height: 22px;

      @include vp-767 {
        font-size: 16px;
        line-height: 20px;
      }
    }

    .card-news__title {
      margin-bottom: 20px;

      font-size: 40px;
      line-height: 50px;
      color: $color-default-white;

      @include vp-767 {
        font-size: 20px;
        line-height: 24px;
      }
    }

    .card-news__description {
      margin: 0;

      display: block;
      font-size: 20px;
      line-height: 24px;
      color: $color-default-white;

      @include vp-767 {
        font-size: 18px;
        line-height: 22px;
      }
    }

    .card-news__date {
      order: -1;
      margin: 0 0 15px;

      font-size: 20px;
      line-height: 24px;

      @include vp-767 {
        font-size: 16px;
        line-height: 18px;
      }
    }

    .link {
      margin-top: 45px;
      margin-left: 0;

      width: max-content;

      color: $color-default-white;
      transition: $trans-default;

      @media (hover: hover), screen and (min-width: 0\0) {
        &:hover,
        &:focus {
          opacity: 0.8;
        }
      }

      @include vp-767 {
        margin-top: 25px;
      }
    }
  }

  .card-news:nth-child(2) {
    grid-column-start: 3;
    grid-column-end: 5;
    grid-row-start: 1;
    grid-row-end: 2;

    @include vp-1439 {
      grid-column-start: 1;
      grid-column-end: 3;
      grid-row-start: 3;
      grid-row-end: 4;
    }

    @include vp-1279 {
      grid-column-start: unset;
      grid-column-end: unset;
      grid-row-start: unset;
      grid-row-end: unset;
    }
  }

  .card-news:nth-child(3) {
    grid-column-start: 3;
    grid-column-end: 5;
    grid-row-start: 2;
    grid-row-end: 3;

    @include vp-1439 {
      grid-column-start: 3;
      grid-column-end: 5;
      grid-row-start: 3;
      grid-row-end: 4;
    }

    @include vp-1279 {
      grid-column-start: unset;
      grid-column-end: unset;
      grid-row-start: unset;
      grid-row-end: unset;
    }
  }

  .card-news:nth-child(2),
  .card-news:nth-child(3) {
    @include vp-1279-mf {
      flex-direction: row;

      .card-news__img {
        flex-shrink: 0;
        height: 100%;
        max-width: 420px;
        width: 50%;
      }

      .card-news__inner {
        padding: 15px 15px 35px 28px;

        @include vp-1439 {
          padding: 10px 15px 20px 25px;

        }
      }

      .card-news__title {
        order: 2;
        margin-bottom: 17px;

        height: 173px;

        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 6;
        overflow: hidden;

        @include vp-1919 {
          -webkit-line-clamp: 7;
        }

        @include vp-1439 {
          margin-bottom: 10px;

          height: 120px;
          -webkit-line-clamp: 5;
        }
      }

      .card-news__info {
        order: 1;
      }

      .card-news__date {
        order: 2;
        margin-top: 10px;
      }

      .card-news__description {
        order: 3;
        margin: 13px 0 26px;

        display: block;

        font-size: 16px;
        line-height: 20px;

        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 6;
        overflow: hidden;

        @include vp-1439 {
          margin: 0 0 20px;
        }
      }

      .link {
        order: 4;
        margin-left: 0;
        margin-top: auto;
      }
    }
  }
}
