.card-news {
  display: flex;
  flex-direction: column;

  border: 1px solid $color-dark-gray;
  box-shadow: 4px 4px 0px 0px $color-silver;

  &__img {
    width: 100%;
    height: 280px;

    @include vp-1439 {
      height: 230px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__inner {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding: 15px 15px 30px;

    @include vp-1439 {
      padding: 10px 10px 20px;
    }

    @include vp-767 {
      padding: 14px;
    }
  }

  &__info {
    margin-bottom: 10px;

    display: flex;
    justify-content: flex-end;
    align-items: flex-end;

    text-transform: uppercase;
    font-size: 14px;
    line-height: 17px;
    font-weight: 700;
    color: $color-alizarin-red;

    @include vp-767 {
      justify-content: flex-start;
    }

    svg {
      margin-right: 5px;

      width: 12px;
      height: 17px;
    }
  }

  &__title {
    margin-bottom: 35px;

    @include vp-1439 {
      margin-bottom: 20px;
    }
  }

  &__description {
    display: none;
  }

  &__date {
    margin-top: auto;

    font-size: 16px;
    line-height: 20px;
    color: $color-dark-gray;
    font-weight: 700;

    @include vp-1439 {
      margin-top: 0;
      margin-bottom: 10px;
    }
  }

  .link {
    margin-top: -19px;
    margin-left: auto;

    font-size: 20px;

    width: max-content;

    svg {
      margin-left: 15px;
    }

    @include vp-1439 {
      margin-top: auto;
      margin-left: 0;

      font-size: 18px;

      svg {
        margin-left: 10px;
      }
    }
  }


}

.card-news--big {
  position: relative;

  .card-news__img {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    width: 100%;
    height: 100%;

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;

      width: 100%;
      height: 100%;
      pointer-events: none;

      background: rgba($color: #000000, $alpha: 0.3);
      background-image: url('../img/content/card-pattern.png');
      background-repeat: repeat;
    }
  }

  .card-news__inner {
    margin-top: auto;
    z-index: 2;
    flex-grow: 0;
  }

  .card-news__info {
    position: absolute;
    top: 30px;
    right: 30px;

    span {
      color: $color-default-white;
    }
  }

  .card-news__title {
    font-size: 40px;
    line-height: 50px;
    color: $color-default-white;
  }

  .card-news__description {
    margin: 0;

    display: block;
    font-size: 20px;
    line-height: 24px;
    color: $color-default-white;
  }

  .card-news__date {
    order: -1;
    margin: 0 0 15px;

    font-size: 20px;
    line-height: 24px;
  }

  .link {
    margin-top: 40px;
    margin-left: 0;

    color: $color-default-white;
  }
}

.card-news--wide {
  flex-direction: row;

  .card-news__description {
    display: block;
  }

  .card-news__img {
    flex-shrink: 0;
    height: 100%;
    width: 435px;
  }

  .card-news__title {
    margin-bottom: 110px;
  }

  .card-news__info {

  }
}
