.product-item {
  max-width: 580px;
  padding: 30px 0 30px 20px;

  border: 1px solid $color-whisper;

  @include vp-1919 {
    min-width: 340px;
    padding: 19px 14px 10px;

    &--solutions .product-item__title {
      margin-bottom: 28px;
    }
  }

  @include vp-1279 {
    max-width: 500px;
    padding: 16px 0 5px 14px;

    &--solutions .product-item__title {
      margin-bottom: 15px;
    }
  }

  @include vp-1023 {
    min-width: 270px;
    padding: 16px 0 20px 10px;
  }

  @include vp-767 {
    max-width: 100%;
    min-width: 275px;
    padding: 16px 9px 14px;
  }

  &__title {
    margin-bottom: 18px;

    font-size: 24px;
    line-height: 32px;

    @include vp-1919 {
      margin-bottom: 15px;
    }

    @include vp-1279 {
      font-size: 20px;
      line-height: 26px;
    }

    @include vp-767 {
      font-size: 16px;
      line-height: 20px;
    }
  }

  &__list {
    margin: 0;
    padding: 0;

    list-style: none;
  }

  &__item {
    margin-bottom: 15px;
    padding-right: 26px;

    display: flex;

    font-size: 14px;
    line-height: 18px;

    svg {
      margin-right: 9px;
    }

    @include vp-1279 {
      padding-right: 15px;
    }

    @include vp-767 {
      margin-bottom: 9px;
    }
  }

  &__link {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;

    text-decoration: underline;
    transition: color $trans-600;

    svg {
      width: 18px;
      height: 24px;
    }

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        color: $color-alizarin-red;
      }
    }
  }

  &--documents .product-item__item:last-child {
    margin-bottom: 0;
  }

  &--solutions .product-item__list {
    display: flex;
    flex-wrap: wrap;
  }

  &--solutions svg {
    width: 7px;
    height: 12px;

    color: $color-alizarin-red;
  }
}
