.pagination {
  display: inline-flex;
  align-items: center;
}

.pagination__button {
  padding: 0;

  background-color: transparent;
  border: 0;
  cursor: pointer;

  transition: border $trans-600;

  &--rectangle {
    width: 20px;
    height: 12px;

    background-color: transparent;
    border: 2px solid $color-default-white;
    outline: none;
    cursor: pointer;

    transition: border $trans-600;

    @include vp-1279 {
      width: 15px;
      height: 8px;

      border-width: 1px;
    }

    &:not(:first-child) {
      margin-left: 10px;
    }

    &:hover,
    &:focus,
    &:active {
      border-color: $color-alizarin-red;
      outline: none;
    }
  }

  &--active {
    background-color: $color-default-white;
    cursor: auto;

    transition: background-color $trans-600;

    pointer-events: none;

    &:hover,
    &:focus,
    &:active {
      border-color: $color-default-white;
    }
  }
}
