.main-content__filters {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
  position: sticky;
  top: 14px;
  padding-top: 50px;
  width: 100%;
  z-index: 100;

  @include vp-1919 {
    padding-top: 40px;
  }

  @include vp-1279 {
    padding-top: 39px;
  }

  @include vp-1023 {
    padding-top: 38px;
  }

  @include vp-767 {
    position: relative;
    top: auto;
    padding-top: 0;
  }
}

.main-content--mt .main-content__filters {
  padding-top: 10px;

  @include vp-1919 {
    padding-top: 0;
  }

  @include vp-1279 {
    padding-top: 0;
  }

  @include vp-1023 {
    padding-top: 0;
  }
}

.filters__btn {
  display: none;

  @include vp-767 {
    display: block;
    margin: 0 0 2px;
    padding: 2px 0 0;
    font-weight: 700;
    font-size: 12px;
    line-height: 15px;
    border-bottom: 2px dashed $color-alizarin-red;
    color: $color-default-black;

    svg {
      color: $color-alizarin-red;
      cursor: pointer;
      transition: transform $trans-default;
    }
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover {
      color: $color-alizarin-red;
    }

    &:focus {
      color: $color-default-black;
    }
  }
}

.filters__wrapper {
  @include vp-767 {
    max-height: 0;
    overflow: hidden;
    transition: max-height $trans-default;
  }
}

.filters__btn--active + .filters__wrapper {
  @include vp-767 {
    height: auto;
  }
}

.filters__btn--active svg {
  transform: rotateX(180deg);
  margin-top: 2px;
}

.filters--active,
.main-content--mt .filters--active {
  padding-top: 50px;

  @include vp-1919 {
    padding-top: 40px;
  }

  @include vp-1439 {
    padding-top: 37px;
  }

  @include vp-1279 {
    padding-top: 37px;
  }

  @include vp-1023 {
    padding-top: 37px;
  }

  @include vp-767 {
    background-color: $color-default-white;
  }
}

.filters--active .filters__wrapper {
  position: relative;
}

.filters__catalog-section {
  display: flex;
  align-items: center;
  transition: all $trans-600;

  @include vp-767 {
    flex-wrap: wrap;
    align-items: flex-start;
  }
}

.filters--active .filters__catalog-section {
  position: absolute;
  bottom: 0;
  left: -129px;
  height: 100%;

  @include vp-1439 {
    left: -109px;
  }

  @include vp-1279 {
    display: none;
  }

  @include vp-767 {
    display: flex;
    position: relative;
    bottom: auto;
    left: auto;
  }
}

.catalog-section__title {
  margin: 1px 15px 0 0;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: $color-default-white;
  text-shadow: 0 1px 0 $color-black-transparent;

  @include vp-1919 {
    margin-top: 2px;
    color: $color-default-black;
  }

  @include vp-1439 {
    margin-top: -1px;
    font-size: 14px;
    line-height: 23px;
  }

  @include vp-767 {
    width: 100%;
    margin-top: 13px;
    margin-bottom: 10px;
    font-size: 12px;
    line-height: 20px;
  }
}

.filters--active .catalog-section__title {
  display: none;

  @include vp-767 {
    display: block;
  }
}

.catalog-section__btn {
  margin: 0;
  padding: 8px 20px;
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
  background-color: $color-black-transparent2;

  &:last-child {
    margin-left: 11px;
  }

  &--active {
    background-color: $color-alizarin-red;
    border-top: 3px solid $color-venetian-red;
    margin-top: -3px;
  }

  @include vp-1919 {
    &--active {
      border-top: 4px solid $color-venetian-red;
    }
  }

  @include vp-1439 {
    padding: 9px 15px 9px;
    font-size: 16px;
    line-height: 20px;

    &--active {
      border-top: 2px solid $color-venetian-red;
    }
  }

  @include vp-767 {
    padding: 8px 6px;

    &--active {
      margin-top: -2px;
    }
  }
}

.filters--active .catalog-section__btn:not(.catalog-section__btn--active) {
  display: none;

  @include vp-767 {
    display: block;
  }
}

.filters--active .catalog-section__btn--active {
  height: 100%;
  display: flex;
  align-items: center;
  border: none;

  @include vp-767 {
    height: auto;
    border-top: 2px solid $color-venetian-red;
  }
}

.filters__section {
  transition: all $trans-default;
}

.main-content .filters__section {
  @include vp-767 {
    margin-top: 10px;
  }
}

.main-content--mt .filters__section {
  @include vp-767 {
    margin-top: 0;
  }
}

.filters__section .container {
  padding: 0;
}

.filters__section form {
  transition: all $trans-600;
}

.filters--active form {
  &::before,
  &::after {
    position: absolute;
    bottom: 0;
    right: 0;
    content: "";
    width: 100vw;
    height: 100%;
    background-color: $color-zambezi;
    z-index: -1;
  }

  &::after {
    right: auto;
    left: 0;
  }

  @include vp-767 {
    &::before,
    &::after {
      display: none;
    }
  }
}

.form-filters {
  position: relative;
  display: inline-flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 1495px;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: $color-default-white;
  border: 2px solid $color-alizarin-red;
  transition: all $trans-600;

  @include vp-1023 {
    justify-content: flex-start;
  }

  @include vp-767 {
    flex-direction: column;
  }
}

.form-filters__item {
  max-width: 295px;
  width: auto;
  display: flex;
  min-height: 56px;
  flex-direction: column;
  justify-content: center;
  padding: 5px 29px 6px;
  border-right: 1px solid $color-whisper;
  border-bottom: 1px solid $color-whisper;

  &:first-child {
    border-left: none;
  }

  @include vp-1919 {
    min-height: 46px;
    padding: 15px 15px 14px;
  }

  @include vp-1439 {
    padding: 15px 14px 14px 15px;
  }

  @include vp-1279 {
    padding-right: 13px;
  }

  @include vp-1023 {
    min-height: 38px;
    padding: 11px 14px 9px 15px;
    border-bottom: 1px solid $color-whisper;
  }

  @include vp-767 {
    width: 100%;
    min-height: fit-content;
    padding: 0;
    border: none;
  }
}

.filters__item--active {
  @include vp-767 {
    background-color: $color-whisper;
  }
}

.form-filters__item.filters-item[data-parent="brands"] {
  padding-left: 28px;
  min-width: 24%;

  @include vp-1919 {
    padding-left: 15px;
    min-width: 13%;
  }

  @include vp-1439 {
    min-width: 10%;
  }

  @include vp-1023 {
    min-width: 37.3%;
  }

  @include vp-767 {
    min-width: 100%;
    position: relative;
    padding: 0;
  }
}

.form-filters__item.filters-item[data-parent="equipment"] {
  min-width: 16.9%;

  @include vp-1919 {
    padding-right: 12px;
    min-width: 18%;
  }

  @include vp-1439 {
    min-width: 19.2%;
    padding-right: 14px;
  }

  @include vp-1279 {
    min-width: 19%;
  }

  @include vp-1023 {
    min-width: 32.1%;
    padding-right: 15px;
  }

  @include vp-767 {
    min-width: 100%;
    padding: 0;
  }
}

.form-filters__item.filters-item[data-parent="power"] {
  min-width: 12.3%;
  padding-right: 28px;

  @include vp-1919 {
    padding-right: 11px;
    min-width: 13.6%;
  }

  @include vp-1439 {
    min-width: 14.4%;
    padding-right: 14px;
  }

  @include vp-1279 {
    min-width: 14%;
    padding-right: 13px;
  }

  @include vp-1023 {
    min-width: 30.2%;
    padding-right: 12px;
    padding-left: 16px;
  }

  @include vp-767 {
    min-width: 100%;
    padding: 0;
  }
}

.form-filters__item.filters-item[data-parent="height"] {
  min-width: 15.4%;

  @include vp-1919 {
    padding-left: 16px;
    padding-right: 10px;
    min-width: 18%;
  }

  @include vp-1439 {
    min-width: 19%;
    padding-right: 14px;
    padding-left: 15px;
  }

  @include vp-1279 {
    min-width: 18%;
  }

  @include vp-1023 {
    min-width: 29.5%;
    padding-left: 14px;
  }

  @include vp-767 {
    min-width: 100%;
    padding: 0;
  }
}

.form-filters__item.filters-item[data-parent="height"],
.form-filters__item.filters-item[data-parent="capacity"] {
  padding-right: 28px;
}

.form-filters__item.filters-item[data-parent="capacity"] {
  min-width: 17.4%;

  @include vp-1919 {
    padding-right: 9px;
    padding-left: 17px;
    min-width: 20%;
  }

  @include vp-1439 {
    min-width: 21.7%;
    padding-right: 15px;
    padding-left: 14px;
  }

  @include vp-1279 {
    min-width: 21%;
  }

  @include vp-1023 {
    min-width: 32.7%;
  }

  @include vp-767 {
    min-width: 100%;
    padding: 0;
  }
}

.filters__item--active.filters-item[data-parent="capacity"] {
  @include vp-1023 {
    padding-right: 15px;
    padding-left: 14px;
  }

  @include vp-767 {
    padding: 0;
  }
}

.form-filters__item.filters-item[data-parent="price"] {
  padding-right: 26px;
  min-width: 14%;

  @include vp-1919 {
    padding-right: 8px;
    padding-left: 21px;
    min-width: 17%;
  }

  @include vp-1439 {
    min-width: 15.6%;
    padding-right: 8px;
    padding-left: 13px;
  }

  @include vp-1023 {
    min-width: 37.7%;
    padding-right: 15px;
    padding-left: 15px;
  }

  @include vp-767 {
    min-width: 100%;
    padding: 0;
  }
}

.filters-item__btn {
  margin: 0;
  padding: 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  display: flex;
  align-items: center;
  color: $color-zambezi;
  width: fit-content;
  cursor: pointer;
  transition: opacity $trans-600;

  @include vp-1919 {
    font-size: 14px;
    line-height: 17px;
  }

  @include vp-1439 {
    font-size: 12px;
  }

  @include vp-1279 {
    font-size: 14px;
  }

  @include vp-767 {
    width: 100%;
    padding: 10px 8px 10px 10px;
    justify-content: space-between;
    border-top: 1px solid $color-whisper;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:active {
      opacity: 0.6;
    }
  }
}

.filters__item--active .filters-item__btn {
  @include vp-767 {
    font-weight: 700;
    color: $color-default-black;
  }
}

.filters-item__icon {
  transition: transform $trans-600;
  margin-top: -4px;
  margin-left: 5px;
  color: $color-dark-gray;
  display: flex;
  width: 12px;
  height: 7px;

  @include vp-1919 {
    margin-top: -1px;
  }

  @include vp-1439 {
    width: 10px;
  }
}

.filters-item[data-parent="price"] .filters-item__icon {
  @include vp-1919 {
    margin-top: -2px;
  }
}

.filters__item--active .filters-item__icon {
  transform: rotateX(180deg) translate3d(0, 0, 0);
  margin-top: -1px;
}

.active-icons {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;

  @include vp-1279 {
    display: none;
  }
}

.active-icon {
  display: none;
  position: relative;
  color: $color-default-white;
  background-color: $color-dark-gray;
  border-radius: 4px;
  margin-top: 5px;
  margin-left: 5px;
  transform: translateX(-5px);
  transition: opacity $trans-600;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      opacity: 0.5;
    }
  }

  &--active {
    display: block;
    z-index: 100;
  }

  &[data-parent="price"] {
    max-width: 147px;
  }
}

.active-icon__button {
  position: relative;
  transform: translateY(1px);
  display: block;
  text-align: left;
  width: fit-content;
  margin: 0;
  padding: 0 24px 0 8px;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;

  &::after,
  &::before {
    position: absolute;
    top: 8px;
    right: 4px;
    content: "";
    display: block;
    width: 13px;
    height: 1.5px;
    background-color: $color-default-white;
    transform: rotateZ(45deg);
  }

  &::after {
    transform: rotateZ(-45deg);
  }
}

.active-icon[data-category="equipment"] .active-icon__button,
.active-icon[data-category="power"] .active-icon__button {
  text-transform: capitalize;
}

.filters-item__list {
  position: absolute;
  top: calc(100% + 2px);
  left: 0;
  margin: 0;
  padding: 0 15px 0;
  list-style: none;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  border: 1px solid $color-whisper;
  background-color: $color-default-white;
  transition: transform $trans-default;
  transform: scaleY(0);
  transform-origin: 50% 0;
  overflow-y: auto;

  @include vp-1919 {
    font-size: 14px;
    line-height: 17px;
  }

  @include vp-767 {
    position: relative;
    top: auto;
    left: auto;
    max-height: 0;
    border: none;
    overflow: hidden;
    transform: none;
    transition: max-height $trans-default;
  }
}

.form-filters__item:not(:first-child) .filters-item__list {
  @include vp-767 {
    width: 100%;
  }
}

.filters__item--active .filters-item__list {
  transform: scaleY(1);

  @include vp-767 {
    transform: none;
  }
}

.brands-list {
  max-width: 1282px;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  border-top: 1px solid $color-whisper;
  border-left: 1px solid $color-whisper;

  @include vp-767 {
    border: none;
    left: auto;
  }
}

.brands-item {
  position: relative;
  width: 100%;
  max-width: 320px;
  padding: 29px 20px 20px;
  border: 1px solid $color-whisper;
  border-top: none;
  border-left: none;

  &--active {
    background-color: $color-white-smoke;
  }

  @include vp-1710 {
    max-width: 33.3%;
  }

  @include vp-1023 {
    max-width: 50%;
  }

  @include vp-767 {
    max-width: 100%;
    padding: 15px 8px;
  }
}

.brands-item .logo-wrapper {
  position: absolute;
  top: 16px;
  left: 44px;

  @include vp-767 {
    left: 33px;
  }

  &--lgmg,
  &--haulotte,
  &--manitou,
  &--dingli {
    top: 24px;
  }

  &--zoomlion,
  &--genie,
  &--magni,
  &--goman,
  &--jlg {
    top: 19px;
  }

  &--platform,
  &--nifty {
    top: 20px;
  }
}

.brands-item .logo-wrapper--lgmg {
  width: 104px;
  height: 20px;
}

.brands-item .logo-wrapper--zoomlion {
  width: 87px;
  height: 30px;
}

.brands-item .logo-wrapper--haulotte {
  width: 132px;
  height: 20px;
}

.brands-item .logo-wrapper--genie {
  width: 80px;
  height: 30px;
}

.brands-item .logo-wrapper--manitou {
  width: 113px;
  height: 30px;
}

.brands-item .logo-wrapper--magni {
  width: 95px;
  height: 30px;
}

.brands-item .logo-wrapper--dingli {
  width: 89px;
  height: 30px;
}

.brands-item .logo-wrapper--goman {
  width: 152px;
  height: 30px;
}

.brands-item .logo-wrapper--platform {
  width: 111px;
  height: 30px;
}

.brands-item .logo-wrapper--nifty {
  width: 80px;
  height: 28px;
}

.brands-item .logo-wrapper--jlg {
  width: 90px;
  height: 30px;
}

.brands-item img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: left;
}

.brands-item .brands-item__description {
  margin: 30px 0 10px 24px;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: $color-zambezi;

  @include vp-767 {
    margin: 40px 0 7px;
  }
}

.brands-item__buttons {
  display: flex;
  align-items: center;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;

  @include vp-767 {
    font-size: 12px;
    line-height: 15px;
  }
}

.brands-item__buttons .custom-toggle {
  color: $color-default-white;
  padding: 0;
  min-height: 27px;
  min-width: 102px;
  box-sizing: border-box;
  width: auto;
  align-items: center;
  justify-content: center;

  @include vp-767 {
    min-width: 90px;
    min-height: 25px;
  }
}

.brands-item__buttons label {
  padding: 0;
  margin: 0;
}

.brands-item__buttons .custom-toggle__icon {
  position: absolute;
  top: 29px;
  left: 20px;
  width: 12px;
  height: 12px;
  border: 1px solid $color-dark-gray;
  box-sizing: border-box;
  transition: opacity $trans-600;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      opacity: 0.6;
    }
  }

  @include vp-767 {
    left: 10px;
  }

  &::before {
    position: absolute;
    top: -6px;
    left: 1px;
    content: url("../img/sprite/icon-check.svg");
    width: 11px;
    height: 14px;
    opacity: 0;
    transition: opacity $trans-default;
  }
}

.brands-item__buttons input:checked + .custom-toggle__icon {
  border: 1px solid $color-dark-gray;

  &::before {
    background-color: transparent;
    opacity: 1;
    transition: opacity $trans-default;
  }
}

.brands-item__buttons .custom-toggle__label {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 27px;
  min-width: 102px;
  padding: 0 10px;
  background-color: $color-default-black;
  transition: opacity $trans-600;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      opacity: 0.6;
    }
  }

  @include vp-767 {
    min-width: 90px;
    min-height: 25px;
    padding: 0;
  }

  &::before {
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    content: "Убрать";
    background-color: $color-default-black;
    z-index: 1;
    transition: opacity $trans-default;
  }
}

.brands-item__buttons input:checked {
  & ~ .custom-toggle__label::before {
    opacity: 1;
    transition: opacity $trans-default;
  }
}

.brands-item__link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 20px;
  transition: opacity $trans-600;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      opacity: 0.6;
    }
  }
}

.brands-item__link svg {
  margin-left: 5px;
}

.brands-sublist {
  position: absolute;
  top: 100%;
  left: 0;
  min-width: 490px;
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: $color-default-white;
  transition: transform $trans-default;
  transform: scaleY(0);
  transform-origin: 50% 0;

  &--active {
    transform: scaleY(1);
  }

  @include vp-767 {
    position: relative;
    top: auto;
    left: auto;
    transform: none;
    min-width: 100%;
    max-height: 0;
    display: flex;
    flex-direction: column;
    overflow: hidden;

    &--active {
      max-height: 100%;
    }
  }
}

.brands-sublist--active ~ .brands-list {
  transform: scaleY(0);

  @include vp-767 {
    display: none;
  }
}

.brands-sublist .brands-item {
  position: absolute;
  top: 2px;
  left: 0;
  min-width: 490px;
  padding: 29px 58px 29px 20px;
  border: 1px solid $color-whisper;
  background-color: $color-default-white;
  display: none;

  &--active {
    display: block;
  }

  @include vp-767 {
    position: relative;
    top: auto;
    left: auto;
    min-width: 100%;
    padding: 0;
    transform: none;
    max-height: 0;
    overflow: hidden;
    border: none;

    &--active {
      max-height: 100%;
    }
  }
}

.brands-sublist .brands-item__content {
  @include vp-767 {
    padding: 15px 8px;
  }
}

.brands-sublist .brands-item__link {
  &:first-child {
    display: flex;
    flex-direction: row-reverse;
    width: fit-content;
    margin-left: 0;
    margin-bottom: 30px;
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    text-transform: uppercase;
    justify-content: flex-end;
    cursor: pointer;

    & svg {
      transform: rotate(180deg);
      width: auto;
      margin: 0;
      margin-right: 5px;
    }
  }

  &:last-child {
    border: 2px solid $color-default-black;
    min-height: 27px;
    margin-left: 0;
    padding: 2px 14px 2px 15px;

    & svg {
      display: none;
    }
  }

  @include vp-767 {
    font-size: 12px;
    line-height: 15px;

    &:first-child {
      margin-left: 0;
    }

    &:last-child {
      text-align: center;
      margin: 0;
      padding: 2px 8px 2px 8px;
    }
  }
}

.brands-sublist .logo-wrapper {
  position: relative;
  top: auto;
  left: auto;
  width: 207px;
  height: 40px;
  margin: 0 0 20px -1px;
  margin-bottom: 20px;

  @include vp-767 {
    left: auto;
  }
}

.brands-sublist .brands-item__description {
  margin: 0 0 15px;
  font-size: 14px;
  line-height: 17px;

  &:last-child {
    margin-bottom: 30px;
  }

  @include vp-767 {
    font-size: 12px;
    line-height: 16px;
    margin: 0 0 15px;

    &:last-child {
      margin-bottom: 30px;
    }
  }
}

.brands-sublist .brands-item__buttons {
  justify-content: flex-start;
}

.brands-sublist .custom-toggle {
  margin-right: 17px;

  @include vp-767 {
    margin-right: 8px;
  }
}

.brands-sublist .custom-toggle__icon {
  display: none;
}

.brands-sublist .custom-toggle__label {
  @include vp-767 {
    min-height: 38px;
  }
}

.filters-item[data-parent="equipment"] .filters-item__list {
  min-width: 16.5%;

  @include vp-1919 {
    min-width: 18.4%;
  }

  @include vp-1439 {
    min-width: 20%;
  }

  @include vp-767 {
    width: 100%;
  }
}

.filters-item[data-parent="equipment"] .checkbox-item,
.filters-item[data-parent="power"] .checkbox-item {
  position: relative;
  padding-left: 21px;

  &:not(:last-child) {
    margin-bottom: 5px;
  }

  &:first-child {
    margin-top: 14px;
  }

  &:last-child {
    margin-bottom: 15px;
  }
}

.checkbox-item label {
  cursor: pointer;
}

.filters-item[data-parent="equipment"] span,
.filters-item[data-parent="power"] span {
  position: absolute;
  top: 3px;
  left: 0;
  width: 12px;
  height: 12px;
  border: 1px solid $color-dark-gray;
  box-sizing: border-box;

  &::before {
    position: absolute;
    top: -6px;
    left: 1px;
    content: url("../img/sprite/icon-check.svg");
    width: 11px;
    height: 14px;
    opacity: 0;
    transition: opacity $trans-default;
  }
}

.filters-item[data-parent="equipment"] .checkbox-item__input:checked + span,
.filters-item[data-parent="power"] .checkbox-item__input:checked + span {
  border: 1px solid $color-dark-gray;

  &::before {
    background-color: transparent;
    opacity: 1;
    transition: opacity $trans-default;
  }
}

.filters-item[data-parent="power"] {
  min-width: 12%;

  @include vp-1023 {
    min-width: 20%;
  }

  @include vp-767 {
    width: 100%;
  }
}

.cost-list {
  width: auto;
}

.filters-item[data-parent="height"] .filters-item__list,
.filters-item[data-parent="capacity"] .filters-item__list,
.filters-item[data-parent="cost"] .filters-item__list {
  padding: 0;
  font-size: 12px;
  line-height: 15px;
  color: $color-dark-gray;

  @include vp-1919 {
    font-size: 12px;
    line-height: 15px;
  }
}

.filters-item[data-parent="height"] .filters-item__list {
  min-width: 15%;

  @include vp-1919 {
    min-width: 18%;
  }

  @include vp-1439 {
    min-width: 19%;
  }

  @include vp-1023 {
    min-width: 22%;
  }

  @include vp-767 {
    width: 100%;
  }
}

.filters-item[data-parent="capacity"] .filters-item__list {
  min-width: 16.8%;

  @include vp-1919 {
    min-width: 20.4%;
  }

  @include vp-1439 {
    min-width: 21.6%;
  }

  @include vp-1023 {
    min-width: 23%;
  }

  @include vp-767 {
    width: 100%;
  }
}

.form-range__list {
  display: flex;
}

.form-range__item {
  display: flex;
  align-items: center;
  width: 50%;
  padding: 7px 18px 3px;
  border-bottom: 1px solid $color-whisper;

  &:first-child {
    border-right: 1px solid $color-whisper;
  }
}

.form-range input {
  border: none;
  margin-left: 5px;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: $color-default-black;
  outline: none;
  overflow: hidden;
}

.js-range-slider {
  padding: 13px 19px 14px;
}

.form-range .noUi-horizontal {
  height: 2px;
  width: 83%;
  border: 0;
  margin-top: 5px;
  margin-bottom: 5px;
}

.form-range .noUi-base {
  background-color: $color-whisper;
  height: 2px;
  cursor: pointer;
}

.form-range .noUi-connect {
  height: 2px;
  background-color: $color-zambezi;
}

.form-range .noUi-handle {
  width: 10px;
  height: 10px;
  right: -4px;
  top: -4px;
  border: 0;
  border-radius: 50%;
  box-shadow: none;
  outline: none;
  background-color: $color-alizarin-red;
  cursor: pointer;

  &::before,
  &::after {
    display: none;
  }
}
