.main-header {
  border-bottom: 1px solid $color-whisper;

  // используйте на страницах специальный модификатор
  // --no-bb (border-bottom). Название --product не несет никакого смысла
  // TODO замените product на no-bb
  // аналогично с inner
  &--no-bb {
    border-bottom: none;
  }

  &--border-red {
    border-bottom: $border-red;
  }

  &--image {
    position: relative;
    border-bottom: none;
  }
}

.main-header .container {
  padding: 28px 60px 38px;

  @include vp-1919 {
    padding-top: 31px;
    padding-bottom: 23px;
  }

  @include vp-1439 {
    padding: 30px 30px 29px;
  }

  @include vp-1023 {
    padding: 30px 29px 29px;
  }

  @include vp-767 {
    padding: 15px 15px 14px;
  }
}

.main-header--image .container {
  min-height: 275px;
  padding-bottom: 80px;

  @include vp-1919 {
    min-height: 205px;
    padding-bottom: 40px;
  }

  @include vp-1439 {
    min-height: 178px;
  }

  @include vp-1023 {
    padding-top: 26px;
    min-height: 174px;
  }

  @include vp-767 {
    min-height: 172px;
  }
}

.main-header--no-bb .container {
  @include vp-1439 {
    padding-bottom: 15px;
  }

  @include vp-1023 {
    padding-bottom: 15px;
  }
}

.main-header--min-mb .container {
  padding-bottom: 20px;

  @include vp-1919 {
    padding-bottom: 4px;
  }

  @include vp-1439 {
    padding-bottom: 4px;
  }

  @include vp-1023 {
    padding-bottom: 4px;
  }

  @include vp-767 {
    padding-bottom: 4px;
  }
}

.main-header__image {
  position: absolute;
  top: -3px;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  z-index: -1;

  &::before {
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    width: 100%;
    height: 100%;
    background-image: $black-to-right3;
  }

  @include vp-1919 {
    top: 0;
  }
}

.main-header__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.breadcrumbs {
  margin-bottom: 40px;

  @include vp-1439 {
    margin-bottom: 31px;
  }

  @include vp-767 {
    margin-bottom: 12px;
  }
}

.main-header--no-bb .breadcrumbs {
  @include vp-1919 {
    margin-bottom: 30px;
  }

  @include vp-767 {
    margin-bottom: 12px;
  }
}

.breadcrumbs__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;

  @include vp-1439 {
    font-size: 14px;
    line-height: 18px;
  }
}

.breadcrumbs__item {
  position: relative;

  &:not(:last-child) {
    margin-right: 39px;
  }

  &:not(:last-child)::after {
    content: "";
    position: absolute;
    top: 10px;
    right: -29px;
    width: 20px;
    height: 1px;
    background-color: $color-zambezi;
  }

  @include vp-1439 {
    &:not(:last-child) {
      margin-right: 41px;
    }
  }

  @include vp-1023 {
    &:not(:last-child) {
      margin-right: 45px;
    }

    &:not(:last-child)::after {
      top: 11px;
      right: -35px;
    }
  }

  @include vp-767 {
    margin-bottom: 3px;

    &:not(:last-child) {
      margin-right: 41px;
    }

    &:not(:last-child)::after {
      top: 9px;
    }
  }
}

.main-header--image .breadcrumbs__item {
  &::after {
    background-color: $color-default-white;
    text-shadow: 0 1px 0 $color-black-transparent;
  }
}

.breadcrumbs__link {
  position: relative;
  color: $color-default-black;
  outline: none;
  transition: opacity $trans-600;

  &::before {
    position: absolute;
    bottom: 1px;
    left: 0;
    content: "";
    width: 100%;
    height: 1px;
    background-color: $color-default-black;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      opacity: 0.6;
    }
  }

  &--current {
    color: $color-zambezi;

    &::before {
      display: none;
    }

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        opacity: 1;
      }
    }
  }

  @include vp-1439 {
    &::before {
      bottom: 2px;
    }
  }
}

.main-header--image .breadcrumbs__link {
  color: $color-default-white;
  text-shadow: 0 1px 0 $color-black-transparent;

  &::before {
    background-color: $color-default-white;
    text-shadow: 0 1px 0 $color-black-transparent;
  }
}

.main-header__title {
  margin-bottom: 20px;

  &--no-mb {
    margin-bottom: 0;
  }

  @include vp-1919 {
    margin-bottom: 19px;

    &--no-mb {
      margin-bottom: 0;
    }
  }

  @include vp-1439 {
    margin-bottom: 15px;

    &--no-mb {
      margin-bottom: 0;
    }
  }

  @include vp-767 {
    margin-bottom: 16px;
    font-size: 24px;

    &--no-mb {
      margin-bottom: 0;
    }
  }
}

.main-header--image .main-header__title {
  color: $color-default-white;
  text-shadow: 2px 2px 0 $color-black-transparent;
}

.main-header--no-bb .main-header__title {
  @include vp-1919 {
    margin-bottom: 15px;
  }

  @include vp-767 {
    margin-bottom: 11px;
  }
}

.main-header__description {
  max-width: 580px;
  margin: 0 0 41px;
  padding-left: 1px;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: $color-zambezi;

  @include vp-1919 {
    margin-left: -1px;
  }

  @include vp-1439 {
    max-width: 480px;
    margin-bottom: 29px;
    font-size: 16px;
    line-height: 20px;
  }

  @include vp-1023 {
    font-size: 14px;
    line-height: 17px;
  }

  @include vp-767 {
    margin-bottom: 14px;
  }
}

.main-header--no-bb .main-header__description {
  @include vp-1919 {
    max-width: 480px;
    margin-bottom: 15px;
    font-size: 16px;
    line-height: 20px;
  }

  @include vp-1439 {
    margin-bottom: 14px;
  }

  @include vp-1023 {
    font-size: 16px;
    line-height: 20px;
  }
}

.main-header__link {
  width: max-content;
  max-width: 200px;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  outline: none;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  transition: color $trans-600;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      color: $color-alizarin-red;
    }
  }

  @include vp-1919 {
    margin-left: 1px;
  }

  @include vp-1439 {
    margin-left: -3px;
    font-size: 14px;
    line-height: 18px;
  }
}

.main-header--no-bb .main-header__link {
  @include vp-1919 {
    margin-left: -1px;
  }

  @include vp-1439 {
    margin-left: -3px;
  }
}

.main-header__icon {
  width: 27px;
  min-width: 27px;
  height: 27px;
  margin-left: 1px;
  margin-right: 10px;
  color: $color-alizarin-red;

  @include vp-1919 {
    margin-top: 1px;
  }

  @include vp-1439 {
    margin-top: 3px;
    margin-right: 12px;
    width: 23px;
    height: 23px;
  }
}
