.feedback-form {
  position: relative;
  padding: 141px 60px 101px;
  background-color: $color-white-smoke;

  @include vp-1919 {
    padding-top: 101px;
    padding-bottom: 59px;
  }

  @include vp-1279 {
    padding: 60px 30px 30px;
  }

  @include vp-767 {
    padding: 60px 15px 30px;
  }

  &.js-animation {
    .feedback-form::before {
      display: none;
    }

    .feedback-form__title {
      position: relative;

      width: max-content;
    }
  }

  &::before {
    position: absolute;
    left: 0;
    top: 100px;
    content: "";
    width: 349px;
    height: 2px;
    background-color: $color-alizarin-red;

    @include vp-1919 {
      top: 60px;
    }

    @include vp-1279 {
      width: 270px;
      top: 30px;
    }

    @include vp-767 {
      width: 207px;
    }
  }

  form {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 30px;
    row-gap: 18px;
    max-width: 570px;

    @include vp-1439 {
      row-gap: 20px;
    }

    @include vp-767 {
      display: flex;
      flex-direction: column;
      row-gap: 0;
      max-width: 100%;
    }
  }
}

.feedback-form__title {
  margin-bottom: 32px;

  @include vp-1919 {
    margin-bottom: 31px;
    font-size: 24px;
    line-height: 30px;
  }

  @include vp-1439 {
    font-size: 20px;
    line-height: 24px;
    margin-bottom: 16px;
  }

  @include vp-1279 {
    font-size: 20px;
    line-height: 24px;
  }

  @include vp-767 {
    margin-bottom: 14px;
    font-size: 16px;
    line-height: 20px;
  }
}

.feedback-form__decor-line {
  position: absolute;
  right: 0;
  top: -40px;

  width: calc(100% + 35vw);
  height: 2px;
  background-color: $color-alizarin-red;

  transform-origin: left;

  @include vp-1919 {
    width: calc(100% + 18vw);
    top: -30px;
  }

  @include vp-1279 {
    top: -20px;
  }

  @include vp-1023 {
    top: -22px;
  }

  @include vp-767 {
    top: -16px;
  }
}

.feedback-form__subtitle {
  max-width: 410px;
  margin: 0 0 42px;
  font-weight: 500;
  font-size: 20px;
  line-height: 26px;

  @include vp-1919 {
    max-width: 570px;
    margin-bottom: 38px;
  }

  @include vp-1439 {
    margin-bottom: 31px;
    max-width: 427px;
    font-size: 16px;
    line-height: 20px;
  }

  @include vp-767 {
    font-size: 14px;
    line-height: 18px;
    margin-bottom: 16px;
    max-width: 100%;
  }
}

.feedback-form__field {
  position: relative;
  display: flex;
  flex-direction: column;

  @include vp-767 {
    margin-bottom: 15px;

    &--company {
      margin-bottom: 19px;
    }

    &--phone {
      margin-bottom: 20px;
    }
  }
}

.feedback-form input {
  height: 41px;
  padding: 9px 15px;
  font-weight: 500;
  font-size: 16px;
  line-height: 40px;
  background-color: $color-default-white;
  border: 2px solid $color-whisper;
  transition: border $trans-600;
  outline: none;

  &::placeholder {
    line-height: 40px;
    color: $color-dark-gray;
  }
}

.feedback-form textarea {
  grid-column-start: 1;
  grid-column-end: 3;
  width: 100%;
  max-width: 405px;
  max-height: 122px;
  padding: 12px 17px;
  font-weight: 500;
  font-size: 16px;
  line-height: 16px;
  background-color: $color-default-white;
  border: 2px solid $color-whisper;
  cursor: pointer;
  transition: border $trans-600;
  outline: none;

  &::placeholder {
    line-height: inherit;
    color: $color-dark-gray;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      border-color: $color-dark-gray;
    }
  }

  @include vp-1439 {
    padding: 11px 16px;
    max-height: 120px;
  }

  @include vp-767 {
    margin-bottom: 16px;
  }
}

.is-invalid input:not(.input-company) {
  border-color: $color-alizarin-red;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      border-color: $color-alizarin-red;
    }
  }
}

.feedback-form__warning {
  position: absolute;
  bottom: -17px;
  left: 0;
  font-size: 12px;
  line-height: 14px;
  color: $color-alizarin-red;
  opacity: 0;
  transition: opacity $trans-600;
}

.is-invalid .feedback-form__warning {
  opacity: 1;
}

.feedback-form .custom-toggle {
  position: relative;
  grid-column-start: 1;
  grid-column-end: 3;
  width: max-content;
  margin-top: -2px;
  margin-bottom: 2px;
  padding: 0;

  &.is-invalid {
    &::after {
      position: absolute;
      bottom: 0;
      left: 0;
      content: "";
      width: 100%;
      height: 2px;
      background-color: $color-alizarin-red;
    }
  }

  & label {
    padding-left: 20px;
  }

  @include vp-1439 {
    margin-top: -3px;
    margin-bottom: 0;
  }

  @include vp-767 {
    margin-bottom: 14px;
  }
}

.feedback-form .custom-toggle__label {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  cursor: pointer;
  transition: color $trans-600;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      color: $color-alizarin-red;
    }
  }

  @include vp-767 {
    display: block;
    max-width: 170px;
    padding-top: 4px;
  }
}

.feedback-form .custom-toggle__icon {
  position: absolute;
  top: 6px;
  left: 0;
  width: 12px;
  height: 12px;
  border: 1px solid $color-alizarin-red;
  background-color: $color-default-white;
  box-sizing: border-box;

  &::before {
    position: absolute;
    top: -9px;
    left: 1px;
    content: url("../img/sprite/icon-check.svg");
    width: 11px;
    height: 14px;
    opacity: 0;
    transition: opacity $trans-default;
  }
}

.feedback-form input:checked + .custom-toggle__icon {
  border: 1px solid $color-alizarin-red;

  &::before {
    background-color: transparent;
    opacity: 1;
    transition: opacity $trans-default;
  }
}

.feedback-form__btn {
  width: max-content;
  min-width: 151px;
  margin: 0;
  padding: 12px 30px;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;

  @include vp-767 {
    min-width: 120px;
    padding: 7px 15px;
  }
}
