.form-main {
  position: relative;

  width: 100%;
  min-height: 707px;
  margin: 0 auto;

  background-color: $color-night-rider;

  @include vp-1919 {
    min-height: 676px;
  }

  @include vp-1279 {
    min-height: 554px;
  }

  @include vp-767 {
    min-height: 677px;
  }

  img {
    position: absolute;
    top: 0;

    width: 100%;
    height: 100%;
    margin: 0 auto;

    @include vp-767 {
      object-position: 90% 0;
    }
    object-fit: cover;
    object-position: right;
  }

  .feedback-form {
    max-width: 1398px;
    margin: 0 auto;
    padding: 90px 60px 0;

    color: $color-default-white;

    background-color: $color-transparent;

    @include vp-1919 {
      max-width: 100%;
    }

    @include vp-1279 {
      padding: 50px 30px 0;
    }

    @include vp-767 {
      padding: 45px 15px 0;
    }

    &::before {
      display: none;
    }

    &__title {
      @include vp-1919 {
        margin-bottom: 41px;

        font-size: 20px;
        line-height: 24px;
      }

      @include vp-1279 {
        margin-bottom: 20px;

        font-size: 16px;
        line-height: 20px;
      }

      @include vp-767 {
        margin-bottom: 15px;
      }
    }

    &__subtitle {
      @include vp-1919 {
        max-width: 440px;
        margin-bottom: 60px;

        font-size: 16px;
        line-height: 20px;
      }

      @include vp-1279 {
        max-width: 385px;
        margin-bottom: 39px;

        font-size: 14px;
        line-height: 18px;
      }

      @include vp-767 {
        margin-bottom: 30px;
      }
    }

    &__field {
      @include vp-767 {
        margin-bottom: 20px;
      }
    }

    .custom-toggle {
      @include vp-767 {
        max-width: 100%;

        &__label {
          max-width: 100%;
        }
      }
    }

    &__btn {
      margin-bottom: 20px;

      @include vp-1023 {
        min-width: 121px;
        padding: 12px 22px;
      }
    }
  }
}
