.list {
  margin: 0;
  padding: 0;

  list-style-type: none;

  &--two-columns {
    margin-bottom: 30px;

    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 20px;

    @include vp-1279 {
      grid-template-columns: 1fr 1.25fr;
    }

    @include vp-1023 {
      grid-template-columns: 1fr 1.1fr;
    }

    @include vp-767 {
      display: block;
    }
  }
}

.list__item {
  position: relative;
  padding-left: 15px;

  font-size: 16px;
  line-height: 24px;
  color: $color-vulcan;

  @include vp-1023 {
    font-size: 14px;
    line-height: 22px;
  }

  @include vp-767 {
    font-size: 12px;
    line-height: 20px;
  }

  svg {
    position: absolute;
    top: 0;
    left: 0;

    margin-top: 6px;
    width: 8px;
    height: 10px;

    display: block;

    fill: $color-default-black;
    user-select: none;
  }
}
