.contacts {
  position: relative;
  background-color: $color-white-smoke;

  &__info {
    display: grid;
    grid-template-columns: 1fr 1fr;
    background-color: $color-default-white;

    @include vp-1439 {
      column-gap: 15px;
    }

    @include vp-767 {
      grid-template-columns: 1fr;
    }
  }

  &__info-wrap {
    margin-right: 25px;
    padding: 60px;

    @include vp-1439 {
      padding: 40px 30px;
    }

    @include vp-767 {
      padding: 30px 15px;
    }
  }

  &__info-title,
  &__map-title,
  &__form-title {
    font-size: 32px;
    line-height: 40px;

    @include vp-1439 {
      font-size: 28px;
      line-height: 30px;
    }

    @include vp-1279 {
      font-size: 24px;
      line-height: 26px;
    }

    @include vp-1023 {
      font-size: 20px;
      line-height: 22px;
    }

    @include vp-767 {
      font-size: 18px;
      line-height: 20px;
    }
  }

  &__info-title {
    max-width: 400px;
    margin-bottom: 40px;

    @include vp-1279 {
      max-width: 100%;
    }

    @include vp-767 {
      margin-bottom: 20px;
    }
  }

  &__map-title {
    max-width: 290px;
  }

  &__form-title {
    margin-bottom: 40px;
  }

  &__img {
    position: relative;
    max-width: 960px;
    min-height: 620px;

    @include vp-1439 {
      min-height: 420px;
    }

    @include vp-1279 {
      min-height: 220px;
    }

    @include vp-767 {
      display: none;
    }

    img {
      position: absolute;
      top: 0;
      left: 0;

      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__map-wrap {
    position: relative;
    z-index: 1;
    max-width: 100%;
    min-height: 800px;
    padding: 60px;

    @include vp-1439 {
      min-height: 600px;
      padding: 30px;
    }

    @include vp-1279 {
      min-height: 400px;
      padding: 30px;
    }

    @include vp-767 {
      min-height: 300px;
      padding: 30px 15px;
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;

      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__form {
    max-width: 960px;
    padding: 60px;
    background-color: $color-default-white;

    @include vp-1439 {
      padding: 40px 30px;
    }

    @include vp-767 {
      padding: 30px 15px;
    }
  }
}
