.greeting {
  display: grid;
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  margin-bottom: 101px;
  overflow: hidden;

  grid-template-columns: 960px 960px;

  @include vp-1919 {
    margin-bottom: 70px;

    grid-gap: 62px;
    grid-template-columns: minmax(640px, 960px) minmax(737px, 960px);
  }

  @include vp-1439 {
    margin-bottom: 63px;

    grid-gap: 102px;
    grid-template-columns: 640px minmax(547px, 737px);
  }

  @include vp-1279 {
    margin-bottom: 60px;

    grid-gap: 75px;
    grid-template-columns: minmax(520px, 640px) minmax(437px, 547px);
  }

  @include vp-1023 {
    margin-bottom: 39px;

    grid-gap: 50px;
    grid-template-columns: minmax(340px, 520px) minmax(385px, 437px);
  }

  @include vp-767 {
    margin-bottom: 62px;

    grid-gap: 0;
    grid-template-columns: repeat(1, 1fr);
  }

  &__img {
    max-width: 960px;
    height: 740px;

    @include vp-1919 {
      height: 495px;
    }

    @include vp-1279 {
      height: 400px;
    }

    @include vp-1023 {
      height: 260px;
    }

    @include vp-767 {
      height: 250px;
    }

    img {
      width: 100%;
      height: 100%;

      object-fit: cover;
    }
  }

  &__content {
    width: 100%;
    padding-top: 145px;
    padding-left: 147px;

    @include vp-1919 {
      margin-left: 8px;
      padding-top: 55px;
      padding-left: 10px;
    }

    @include vp-1439 {
      padding-top: 70px;
      padding-left: 0;
    }

    @include vp-1279 {
      padding-top: 74px;
    }

    @include vp-1023 {
      padding-top: 12px;
    }

    @include vp-767 {
      margin-left: 15px;
      padding-top: 30px;
    }

    .title--h2 {
      max-width: 386px;
      margin-top: 41px;
      margin-bottom: 85px;

      @include vp-1919 {
        max-width: 337px;
        margin-top: 19px;
        margin-bottom: 113px;
      }

      @include vp-1279 {
        margin-bottom: 60px;
      }

      @include vp-767 {
        margin-top: 13px;
        margin-bottom: 40px;
      }
    }
  }

  &__content-decor {
    display: block;
    width: 100%;
    height: 2px;

    background: $color-alizarin-red;
  }

  &__text-block {
    position: relative;

    @include vp-767 {
      padding-right: 15px;
    }

    svg {
      position: absolute;
      top: -33px;
      left: -13px;

      width: 120px;
      height: 70px;

      @include vp-1919 {
        width: 103px;
        height: 60px;
      }

      @include vp-1279 {
        top: -24px;
        left: -16px;

        width: 69px;
        height: 40px;
      }

      @include vp-767 {
        top: -10px;
        left: -5px;

        width: 50px;
        height: 30px;
      }
    }
  }

  &__text {
    position: relative;

    width: 100%;
    max-width: 515px;
    margin: 0;
    padding-left: 86px;

    font-weight: 500;
    font-size: 20px;
    line-height: 26px;
    color: $color-zambezi;

    @include vp-1919 {
      max-width: 440px;
      padding-left: 54px;

      font-size: 18px;
      line-height: 24px;
    }

    @include vp-1279 {
      max-width: 370px;
      padding-left: 30px;

      font-size: 16px;
      line-height: 20px;
    }

    @include vp-767 {
      max-width: 100%;
      padding-right: 15px;
      padding-left: 15px;

      font-size: 14px;
      line-height: 18px;
    }
  }
}
