.card-wide {
  position: relative;

  width: 100%;
  max-width: calc(100% - 4px);
  display: flex;
  background-color: $color-snow;
  box-shadow: 4px 4px 0px 0px $color-silver;

  @include vp-1023 {
    display: grid;
  }

  &__img {
    flex-shrink: 0;
    max-width: 580px;
    width: 40%;
    height: 100%;

    @include vp-1439 {
      max-width: 500px;
    }

    @include vp-1279 {
      max-width: 450px;
    }

    @include vp-1023 {
      max-width: 100%;
      width: 100%;
      height: 380px;
    }

    @include vp-767 {
      height: 260px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__date {
    margin-left: 121px;

    padding-top: 40px;
    width: 180px;

    @include vp-1919 {
      margin-left: 100px;
      width: 170px;

    }

    @include vp-1439 {
      margin-left: 70px;
      padding-top: 30px;
      width: 150px;

    }

    @include vp-1279 {
      margin-left: 40px;
      width: 140px;
    }

    @include vp-1023 {
      margin-left: 30px;

      display: flex;
      align-items: flex-end;
      width: 100%;
      padding-top: 20px;
    }

    @include vp-767 {
      margin-left: 16px;
      padding-top: 16px;
    }
  }

  &__day {
    margin-bottom: 10px;
    margin-top: 0;

    display: block;

    font-size: 120px;
    line-height: 85%;
    font-weight: 700;
    color: $color-silver;

    @include vp-1919 {
      font-size: 110px;
    }

    @include vp-1439 {
      font-size: 100px;
    }

    @include vp-1279 {
      font-size: 80px;
    }

    @include vp-1023 {
      margin-bottom: 0;
      margin-right: 10px;

      font-size: 40px
    }

    @include vp-767 {
      margin-right: 8px;

      font-size: 20px;
      line-height: 20px;
    }
  }

  &__mounth,
  &__year {
    margin: 0;

    display: block;

    font-size: 20px;
    line-height: 24px;
    color: $color-zambezi;
    font-weight: 700;

    @include vp-1439 {
      font-size: 18px;
      line-height: 22px;
    }

    @include vp-1279 {
      font-size: 16px;
      line-height: 20px;
    }

    @include vp-1023 {
      margin-right: 6px;
    }
  }

  &__content {
    padding: 40px 60px 34px;
    display: flex;
    flex-direction: column;


    @include vp-1439 {
      padding: 30px 40px 24px;
    }

    @include vp-1023 {
      padding: 20px 30px 24px;
    }

    @include vp-767 {
      padding: 16px;
    }
  }

  &__title {
    margin-bottom: 40px;

    font-size: 32px;
    line-height: 40px;
    color: $color-default-black;
    font-family: $ff-ttautonomous;
    text-transform: uppercase;

    @include vp-1919 {
      font-size: 28px;
      line-height: 36px;
    }

    @include vp-1439 {
      font-size: 22px;
      line-height: 30px;
    }

    @include vp-1279 {
      font-size: 20px;
      line-height: 26px;
    }

    @include vp-1023 {
      margin-bottom: 20px;
    }

    @include vp-767  {
      margin-bottom: 16px;

      font-size: 16px;
      line-height: 22px;
    }
  }

  &__description {
    margin-top: auto;
    margin-bottom: 0;

    font-size: 18px;
    line-height: 24px;
    color: $color-zambezi;

    @include vp-1023 {
      margin-bottom: 20px;
    }

    @include vp-767 {
      margin-bottom: 16px;

      font-size: 16px;
      line-height: 22px;
    }
  }

  .link {
    margin-top: 40px;
    margin-right: auto;

    font-size: 20px;
    line-height: 24px;

    svg {
      width: 40px;
      width: 18px;
    }

    @include vp-767 {
      margin-top: 0;

      font-size: 16px;
      line-height: 20px;

    }
  }
}
