.control {
  align-self: stretch;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  min-height: 60px;
  padding: 0 20px;
  border-left: 1px solid $color-night-rider;
  box-sizing: border-box;
  transition: background-color 0.6s ease;

  @include vp-1919 {
    min-height: 50px;
    padding: 0 15px;
  }

  &--header {
    &:hover {
      background-color: $color-zambezi;
    }

    &:active {
      background-color: $color-night-rider;
    }
  }

  &--mobile {
    @include vp-1279 {
      min-height: 29px;
      margin-bottom: 10px;
      padding: 0;
      border: none;
    }
  }

  &--footer {
    padding: 0;
    border: none;

    &:hover .control__main-info,
    &:hover .control__description,
    &:hover svg {
      fill: $color-silver;
      color: $color-silver;
    }

    &:active .control__main-info,
    &:active .control__description,
    &:active svg {
      fill: $color-dark-gray;
      color: $color-dark-gray;
    }

    @include vp-767 {
      min-height: 47px;
      padding: 0;
    }
  }
}

.control--login {
  max-width: 122px;

  @include vp-1279 {
    max-width: none;
  }
}

.control--header.control--phone {
  @include vp-1919 {
    padding-right: 7px;
  }
}

.control--footer.control--region {
  padding: 0;
}

.control--footer.control--phone {
  @include vp-767 {
    margin-left: 8px;
  }
}

// Блок с иконкой

.control__icon {
  padding-right: 10px;
  line-height: 0;
}

.control--header.control--region .control__icon,
.control--header.control--login .control__icon {
  @include vp-1919 {
    padding-right: 0;
  }
}

.control--region.control--footer .control__icon {
  display: none;
}

.control__icon svg {
  color: $color-alizarin-red;
  width: 16px;
  height: 24px;
  transition: color 0.6s ease;
}

.control--footer svg {
  color: $color-default-white;
}

.control--footer.control--email svg {
  width: 24px;
  height: 17px;
}

// Контент-блок
// Общие стили (Шапка)

.control__content {
  display: flex;
  flex-direction: column;
}

.control--header.control--region .control__content,
.control--header.control--login .control__content {
  @include vp-1919 {
    display: none;
  }
}

.control__main-info {
  margin: 0;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  color: $color-default-white;
  transition: color 0.6s ease;
}

.control--mobile .control__main-info {
  color: $color-default-black;
}

.control__description {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: $color-aluminum;
  transition: color 0.6s ease;

  @include vp-1919 {
    font-size: 10px;
  }
}

// Общие стаили (Подвал)

.control--footer .control__main-info {
  font-weight: 700;
  font-size: 20px;
  line-height: 26px;

  @include vp-1919 {
    font-size: 14px;
    line-height: 18px;
  }

  @include vp-1023 {
    font-size: 12px;
  }
}

.control--footer .control__description {
  font-size: 14px;

  @include vp-1919 {
    font-size: 12px;
    line-height: 12px;
  }

  @include vp-1023 {
    font-size: 10px;
    line-height: 10px;
  }
}

// Телефон (Шапка)

.control--phone .control__main-info {
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;

  @include vp-1919 {
    font-size: 14px;
  }
}

// Телефон (Подвал)

.control--footer.control--phone .control__main-info {
  font-size: 24px;
  line-height: 30px;

  @include vp-1919 {
    font-size: 16px;
    line-height: 20px;
  }

  @include vp-1023 {
    font-size: 14px;
  }
}

// Селект региона (Шапка)

.control--region .control__main-info {
  position: relative;
  order: 2;
  width: min-content;
  color: $color-default-white;
  border-bottom: 2px dashed $color-alizarin-red;

  &::after {
    content: "";
    position: absolute;
    top: 5px;
    right: -15px;
    display: block;
    width: 12px;
    height: 7px;
    background: url("../img/svg/caret.svg") no-repeat right;
  }
}

// Селект региона (Мобильное меню)

.control--mobile.control--region .control__main-info {
  color: $color-default-black;
}

// Селект региона (Подвал)

.control--footer.control--region .control__main-info {
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;

  @include vp-1919 {
    font-size: 16px;
  }

  @include vp-1023 {
    font-size: 14px;
  }
}

.control--footer.control--region .control__description {
  @include vp-1919 {
    font-size: 14px;
    line-height: 17px;
  }
}

// Электронная почта (Подвал)

.control--email .control__main-info {
  order: 2;
}
