.accordion {
  margin: 0 auto;
}

.accordion--sidebar {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 3;
  max-width: 275px;
  width: 275px;
  height: max-content;
  padding: 50px 0;
  box-sizing: border-box;

  @include vp-1919 {
    padding: 40px 0;
  }

  @include vp-1279 {
    margin: 0 auto;
    padding: 0;
    width: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
  }

  @include vp-767 {
    padding-top: 1px;
  }
}

.main-content--mt .accordion--sidebar {
  @include vp-1279 {
    padding-top: 31px;
  }

  @include vp-1023 {
    padding-top: 35px;
  }

  @include vp-767 {
    padding-top: 15px;
  }
}

.main-content--no-mt .accordion--sidebar {
  padding-top: 0;
}

.sidebar__btn {
  display: none;

  @include vp-1279 {
    width: fit-content;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    border: 1px solid $color-dark-gray;
    box-sizing: border-box;
    color: $color-default-black;
    background-color: $color-default-white;
    margin: 0 0 17px;
    padding: 9px 14px;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;

    &--active {
      background-color: $color-whisper;
    }
  }

  @include vp-767 {
    padding: 5px 9px 3px;
    font-size: 16px;
    margin-bottom: 13px;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover {
      opacity: 0.8;
    }
  }
}

.main-content__sidebar.is-active .sidebar__btn {
  background-color: $color-whisper;
}

.sidebar__btn svg {
  width: 32px;
  height: 16px;

  @include vp-1279 {
    margin-right: 16px;
  }
}

.sidebar__container {
  background-color: $color-default-white;
  border: 1px solid $color-whisper;

  @include vp-1279 {
    margin-bottom: 23px;
    display: flex;
    border: none;
    border-top: 1px solid $color-whisper;
  }

  @include vp-1023 {
    margin-bottom: 27px;
  }

  @include vp-767 {
    flex-direction: column;
  }
}

.sidebar__wrapper {
  @include vp-1279 {
    width: 50%;
    border-right: 1px solid $color-whisper;

    &:last-child {
      border-right: none;
    }
  }

  @include vp-767 {
    width: 100%;
    border-right: none;
  }
}

.main-content--product .accordion--sidebar {
  margin-top: 0;

  @include vp-1919 {
    margin-top: 0;
  }

  @include vp-1279 {
    width: 100%;
    margin-left: 0;
    margin-top: 0;

    .sidebar__wrapper {
      width: 100%;
    }
  }

  @include vp-767 {
    width: 100%;
  }
}

.sidebar__list,
.sidebar__sublist {
  margin: 0;
  padding: 0;
  list-style: none;
  transition: all $trans-600;
}

.sidebar__list {
  border-top: 1px solid $color-whisper;

  @include vp-1279 {
    border: 1px solid $color-whisper;
    border-top: none;
  }
}

.sidebar__wrapper:first-child .sidebar__list {
  @include vp-1279 {
    border-right: none;
  }

  @include vp-767 {
    border-right: 1px solid $color-whisper;
  }
}

.sidebar__wrapper:last-child .sidebar__list {
  @include vp-1279 {
    border-left: none;
  }

  @include vp-767 {
    border-left: 1px solid $color-whisper;
  }
}

.sidebar__wrapper:first-child .sidebar__list:first-child {
  border-top: none;
}

.sidebar__list:first-child {
  @include vp-1439 {
    border-top: none;
  }

  @include vp-767 {
    border-top: 1px solid $color-whisper;
  }
}

.sidebar__button {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 13px 17px 11px 19px;
  color: $color-default-black;
  background-color: $color-default-white;
  cursor: pointer;
  border: none;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  transition: all $trans-default;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      opacity: 0.8;
    }
  }

  @include vp-1919 {
    padding: 14px 16px 10px 15px;
  }

  @include vp-1023 {
    padding: 14px 18px 10px 14px;
  }

  @include vp-767 {
    font-size: 14px;
  }
}

.sidebar__list.is-active .sidebar__button {
  background-color: $color-whisper;
  font-weight: 700;
}

.sidebar__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 17px 0 15px;

  @include vp-1919 {
    padding-left: 18px;
    padding-right: 16px;
  }

  @include vp-1023 {
    padding-left: 17px;
    padding-right: 18px;
  }
}

.sidebar__list:first-child .sidebar__item {
  padding-left: 18px;
  padding-right: 16px;

  @include vp-1023 {
    padding-left: 17px;
  }
}

.sidebar__list--active .sidebar__item {
  &:first-child {
    background-color: $color-whisper;
    border-bottom: 1px solid $color-whisper;
    padding-top: 13px;
    padding-bottom: 10px;
  }

  @include vp-1919 {
    &:first-child {
      padding-top: 14px;
      padding-bottom: 11px;
    }

    &:last-child {
      padding-left: 18px;
    }
  }

  @include vp-1023 {
    &:first-child {
      padding-top: 12px;
      padding-bottom: 11px;
    }

    &:last-child {
      padding-left: 17px;
    }
  }
}

.sidebar__list:first-child .sidebar__title {
  @include vp-1919 {
    &:first-child {
      letter-spacing: 0;
    }
  }
}

.sidebar__icon {
  transition: transform $trans-600;
  margin-top: -5px;
  color: $color-dark-gray;
  display: flex;
  width: 12px;
  height: 7px;

  @include vp-1023 {
    margin-top: -1px;
  }
}

.sidebar__list--active .sidebar__title {
  font-weight: 700;
}

.sidebar__list.is-active .sidebar__icon {
  transform: rotateX(180deg);
  margin-top: 2px;
}

.sidebar__sublist {
  padding: 17px 18px 31px;

  @include vp-1919 {
    padding: 19px 18px 28px;
  }

  @include vp-1023 {
    padding-top: 17px;
  }
}

.sidebar__sublist .sublist-item {
  max-width: 100%;
}

.sidebar__sublist .sublist-item:not(:first-child) {
  margin-top: 10px;
}

.sidebar__sublist .sublist-item__link {
  font-size: 16px;
  line-height: 20px;
  text-decoration-skip-ink: none;

  &--active {
    font-weight: 700;
    text-decoration: none;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        color: $color-default-black;
      }
    }
  }

  @include vp-1023 {
    max-width: 216px;
  }

  @include vp-767 {
    max-width: 100%;
    font-size: 14px;
    line-height: 18px;
  }
}

.sidebar__sublist .sublist-item__icon {
  margin-top: 5px;

  @include vp-1919 {
    margin-top: 3px;
  }
}
