.product-price {
  @include vp-767 {
    padding: 0 8px;
  }

  &__title {
    margin: 0;

    font-weight: 400;
    font-size: 14px;
    line-height: 20px;

    color: $color-default-black;
  }

  &__new {
    margin: 0;

    font-weight: 700;
    font-size: 40px;
    line-height: 48px;

    color: $color-alizarin-red;

    span {
      font-size: 24px;
      line-height: 29px;
    }

    @include vp-1279 {
      font-size: 32px;
      line-height: 39px;
    }

    @include vp-767 {
      font-size: 24px;
      line-height: 29px;

      span {
        font-size: 18px;
        line-height: 21px;
      }
    }
  }
}
