.email--footer .email__address {
  font-size: 20px;
  line-height: 26px;
  font-weight: 700;

  @include vp-1919 {
    font-size: 14px;
    line-height: 18px;
  }

  @include vp-1023 {
    font-size: 12px;
  }
}

.email--footer .email__description {
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;

  @include vp-1919 {
    font-size: 12px;
    line-height: 12px;
  }

  @include vp-1023 {
    font-size: 10px;
    line-height: 10px;
  }
}
