.catalog-block {
  margin-bottom: 129px;
  overflow: hidden;

  border-top: $border-grey;
  border-bottom: $border-grey;

  @include vp-1919 {
    margin-bottom: 60px;

    border-top: none;
    border-bottom: none;
  }

  @include vp-1023 {
    margin-bottom: 55px;
  }

  .title--h1 {
    margin-bottom: 200px;
    margin-left: -4px;
    padding-top: 26px;

    @include vp-1919 {
      margin-bottom: 13px;
      padding-top: 0;
    }

    @include vp-1439 {
      margin-bottom: 15px;
    }

    @include vp-1279 {
      margin-bottom: 20px;
    }

    @include vp-767 {
      margin-bottom: 11px;
      margin-left: -2px;
    }
  }

  &__tabs {
    display: flex;

    @include vp-1919 {
      flex-direction: column;

      border-top: none;
      border-bottom: none;
    }
  }

  &__tabs-controls {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    max-width: 453px;
    padding-top: 34px;
    padding-bottom: 34px;
    padding-left: 64px;

    @include vp-1919 {
      flex-direction: row;
      align-items: flex-end;
      max-width: 100%;
      padding: 0 60px 40px;

      border: none;
    }

    @include vp-1279 {
      padding: 0 30px 45px;
    }

    @include vp-1023 {
      padding: 0 30px 30px;
    }

    @include vp-767 {
      flex-direction: column;
      align-items: flex-start;
      padding: 0 15px 15px;
    }
  }

  &__tabs-name {
    display: block;
    margin: 0;
    margin-bottom: 15px;

    font-weight: 500;
    font-size: 18px;
    line-height: 28px;
    color: $color-zambezi;

    @include vp-1919 {
      margin-bottom: 8px;

      font-size: 16px;
      line-height: 24px;
    }

    @include vp-767 {
      font-size: 14px;
      line-height: 20px;
    }
  }

  &__tabs-btns {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    @include vp-1919 {
      flex-direction: row;
      flex-wrap: wrap;
    }

    @include vp-767 {
      margin-bottom: 15px;
    }
  }

  .btn {
    margin: 0 0 15px;
    padding: 4px 15px;

    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    color: $color-zambezi;

    border: 2px solid $color-zambezi;

    transition: color $trans-600;

    @include vp-1919 {
      margin-right: 15px;
      margin-bottom: 0;
      padding: 4px 13px;

      font-size: 16px;
    }

    @include vp-1279 {
      margin-right: 15px;
      padding: 1px 13px;

      font-size: 14px;
    }

    &:last-child {
      @include vp-1919 {
        margin-right: 0;
      }
    }

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        color: $color-alizarin-red;

        background-color: $color-default-white;
        border-color: $color-alizarin-red;
      }
    }
  }

  .btn.is-active {
    color: $color-default-white;

    background-color: $color-alizarin-red;
    border: 2px solid $color-alizarin-red;

    transition: background-color $trans-600;
  }

  .link-download {
    margin-top: auto;
    margin-left: 35px;
  }

  &__tabs-content {
    display: grid;
    margin: 0;
    padding: 0;

    list-style: none;

    border-left: $border-grey;

    grid-template-columns: repeat(4, 367px);

    @include vp-1919 {
      margin-top: 0;
      padding: 0 15px;
      overflow: hidden;

      border-top: $border-grey;
      border-bottom: $border-grey;
      border-left: none;

      grid-template-columns: repeat(4, minmax(351px, 479px));
    }

    @include vp-1439 {
      grid-template-columns: repeat(4, minmax(314px, 359px));
    }

    @include vp-1279 {
      grid-template-columns: repeat(3, minmax(329px, 420px));
    }

    @include vp-1023 {
      padding: 0;

      grid-template-columns: repeat(3, minmax(257px, 341px));
    }

    @include vp-767 {
      grid-template-columns: repeat(2, minmax(290px, 760px));
    }

    @include vp-639 {
      grid-template-columns: repeat(1, minmax(290px, 630px));
    }

    .catalog-block {
      &__item {
        margin-bottom: -2px;
        overflow: hidden;

        border-right: $border-grey;
        border-bottom: $border-grey;

        @include vp-1919 {
          max-width: 100%;
        }

        @include vp-1279 {
          margin-top: 2px;
        }

        @include vp-639 {
          border: none;
          border-bottom: $border-grey;
        }

        &:nth-child(2n) {
          @include vp-767 {
            border-right-color: transparent;
          }
        }

        &:nth-child(3n) {
          @include vp-1279 {
            border-right-color: transparent;
          }

          @include vp-767 {
            border-right-color: $color-whisper;
          }
        }

        &:nth-child(4n) {
          border-right-color: transparent;

          @include vp-1279 {
            border-right-color: $color-whisper;
          }
        }

        .card-block {
          display: flex;
          flex-direction: column;
          height: 100%;
        }

        .card-block__img img {
          transition: transform $trans-default;
        }

        .card-block__img {
          transition: opacity $trans-default;
        }

        .card-block__title {
          display: flex;
          flex: 1 1 auto;
          margin-bottom: 15px;

          font-weight: 700;
          font-size: 20px;
          line-height: 24px;
          color: $color-default-black;
          text-transform: uppercase;
          word-break: break-word;

          background-color: $color-transparent;

          @include vp-1919 {
            font-size: 16px;
            line-height: 20px;
          }

          @include vp-1279 {
            font-size: 14px;
            line-height: 17px;
          }
        }

        .link {
          margin-left: auto;

          @include vp-1023 {
            font-size: 10px;
            line-height: 20px;
          }

          @include vp-767 {
            line-height: 12px;
          }
        }

        svg {
          width: 34px;
          height: 15px;

          @include vp-767 {
            width: 22px;
            margin-left: 9px;
          }
        }
      }
    }

    &--sectors {
      .catalog-block__item {
        min-height: 380px;

        @include vp-1919 {
          min-height: 350px;
        }

        @include vp-1439 {
          min-height: 330px;
        }

        @include vp-1279 {
          min-height: 340px;
        }

        @include vp-1023 {
          min-height: 270px;
        }

        @include vp-767 {
          min-height: 324px;
        }
      }

      .card-block {
        padding: 22px;

        @include vp-1919 {
          padding: 15px;
        }
      }

      .card-block__img {
        position: relative;

        margin-bottom: 15px;
        padding-bottom: percentage(220/320);
        overflow: hidden;
        /* stylelint-disable */
        -webkit-transform: translate3d(0, 0, 0);
        -moz-transform: translate3d(0, 0, 0);
        -webkit-backface-visibility: hidden;
        -moz-backface-visibility: hidden;
        /* stylelint-enable */
        @include vp-1919 {
          width: 100%;
          padding-bottom: percentage(230/322);
        }

        @include vp-1439 {
          padding-bottom: percentage(210/283);
        }

        @include vp-1279 {
          padding-bottom: percentage(224/301);
        }

        @include vp-1023 {
          padding-bottom: percentage(170/226);
        }

        @include vp-767 {
          padding-bottom: percentage(220/290);
        }

        img {
          position: absolute;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;

          width: 100%;
          height: 100%;

          object-fit: cover;
        }

        &::before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          z-index: 1;

          width: 100%;
          height: 100%;

          background: $black-to-right4;
        }
      }

      @media (hover: hover), screen and (min-width: 0\0) {
        .card-block:hover .card-block__img img,
        .card-block:focus .card-block__img img {
          transform: scale(1.12);
        }

        .card-block:hover .card-block__img::before,
        .card-block:focus .card-block__img::before {
          opacity: 0;
        }

        .card-block:hover .link,
        .card-block:focus .link {
          color: $color-alizarin-red;
        }
      }
    }

    &--categories {
      .catalog-block__item {
        min-height: 762px;

        @include vp-1919 {
          min-height: 710px;
        }

        @include vp-1439 {
          min-height: 600px;
        }

        @include vp-1279 {
          min-height: 510px;
        }

        @include vp-1023 {
          min-height: 445px;
        }

        @include vp-767 {
          min-height: 476px;
        }

        @include vp-639 {
          min-height: 393px;
        }
      }

      .card-block {
        position: relative;

        width: 100%;
        max-width: 365px;
        padding: 21px 21px 21px 0;

        background-color: $color-transparent;
        cursor: pointer;

        transition: all $trans-default;

        @include vp-1919 {
          max-width: 100%;
        }

        @include vp-1023 {
          padding: 15px 15px 15px 0;
        }

        &__img {
          position: relative;

          margin-bottom: 15px;
          padding-bottom: percentage(574/320);
          overflow: hidden;
          /* stylelint-disable */
          -webkit-transform: translate3d(0, 0, 0);
          -moz-transform: translate3d(0, 0, 0);
          -webkit-backface-visibility: hidden;
          -moz-backface-visibility: hidden;
          /* stylelint-enable */
          transition: transform $trans-default;

          @include vp-1919 {
            width: 100%;
          }

          @include vp-1439 {
            width: 100%;
            padding-bottom: percentage(502/320);
          }

          @include vp-1279 {
            padding-bottom: percentage(230/180);
          }

          @include vp-767 {
            padding-bottom: percentage(180/170);
          }

          @include vp-639 {
            margin: 0 auto 20px;
            padding-bottom: percentage(180/200);
          }
        }

        &__img img {
          position: absolute;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;

          width: 100%;
          height: 100%;

          object-fit: contain;
          object-position: left;
        }

        &__title {
          padding-left: 21px;

          transition: color $trans-default;
        }

        &__info {
          margin-bottom: 10px;
        }
      }

      @media (hover: hover), screen and (min-width: 0\0) {
        .card-block:hover,
        .card-block:focus {
          background-color: $color-default-black;
        }

        .card-block:hover .card-block__title,
        .card-block:focus .card-block__title {
          color: $color-default-white;
        }

        .card-block:hover .link,
        .card-block:focus .link {
          color: $color-default-white;
        }

        .card-block:hover .card-block__img,
        .card-block:focus .card-block__img {
          transform: scale(1.13);
        }
      }
    }
  }
}
