.gallery-slider {
  position: relative;

  width: 100%;
  padding: 0 25px;

  &__img {
    // width: 245px;
    height: 200px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__prev,
  &__next {
    outline: none;
    padding: 0;

    svg {
      width: 13px;
      height: 22px;

      color: $color-venetian-red2;
    }
    &::after {
      display: none;
    }
  }

  &__prev {
    left: 0;

    justify-content: flex-start;
  }

  &__next {
    right: 0;

    justify-content: flex-end;
  }

}
