.link {
  position: relative;
  display: inline-flex;
  align-items: center;
  text-transform: uppercase;
  font-family: $ff-montserrat;
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;

  @include vp-1919 {
    font-size: 12px;
    line-height: 15px;
  }

  transition: color $trans-default;

  svg {
    margin-left: 5px;
  }

  &--black {
    color: $color-default-black;
  }


  &:disabled {
    opacity: 0.5;

    pointer-events: none;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      color: $color-alizarin-red;
    }
  }


  &--red {
    color: $color-alizarin-red;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        color: $color-default-black;
      }
    }
  }

  &--white {
    color: $color-default-white;
  }
}
