.aurora-main {
  position: relative;
  z-index: 1;

  width: 100%;
  min-height: 900px;
  margin: 0 auto;
  overflow: hidden;

  color: $color-default-white;

  background: $color-alizarin-red;

  @include vp-1919 {
    min-height: 650px;
  }

  @include vp-1279 {
    min-height: 520px;
  }

  @include vp-639 {
    min-height: 666px;
  }

  &__img {
    position: absolute;
    right: 0;
    left: -140px;

    width: 1298px;
    height: 900px;
    margin: 0 auto;

    @include vp-1919 {
      left: -11px;

      width: 938px;
      height: 650px;
    }

    @include vp-1439 {
      left: -114px;
    }

    @include vp-1279 {
      left: 0;

      width: 750px;
      height: 520px;
    }

    @include vp-1023 {
      left: -70px;
    }

    @include vp-639 {
      bottom: 0;
      left: 0;

      width: 404px;
      height: 280px;
    }

    img {
      width: 100%;

      pointer-events: none;

      object-fit: contain;
    }
  }

  .title--h1 {
    padding-top: 60px;
    padding-left: 60px;

    @include vp-1279 {
      padding-top: 32px;
      padding-left: 30px;
    }

    @include vp-767 {
      padding-right: 15px;
    }

    @include vp-639 {
      padding-top: 30px;
      padding-left: 15px;
    }
  }

  &__list {
    display: grid;
    margin: 0;
    padding: 0;

    list-style: none;

    grid-gap: 10px 25px;
    grid-template-columns: repeat(2, 960px);

    @include vp-1919 {
      grid-template-columns: repeat(2, minmax(720px, 960px));
    }

    @include vp-1439 {
      grid-template-columns: repeat(2, minmax(640px, 720px));
    }

    @include vp-1279 {
      grid-template-columns: repeat(2, minmax(512px, 640px));
    }

    @include vp-1023 {
      grid-template-columns: repeat(2, minmax(384px, 512px));
    }

    @include vp-767 {
      grid-template-columns: repeat(2, minmax(320px, 384px));
    }

    @include vp-639 {
      grid-auto-flow: row;
      grid-gap: 5px;
      grid-template-columns: 1fr;
    }
  }

  &__item {
    @include vp-639 {
      margin-bottom: 25px;
      margin-left: 0;
    }
  }

  &__item--text {
    max-width: 460px;
    margin: 30px 0 59px;
    padding-left: 60px;

    font-weight: 500;
    font-size: 20px;
    line-height: 24px;

    @include vp-1919 {
      margin-top: 6px;
      margin-bottom: 18px;

      font-size: 16px;
      line-height: 20px;
    }

    @include vp-1279 {
      margin-top: 21px;
      padding-left: 32px;

      font-size: 14px;
      line-height: 17px;
    }

    @include vp-639 {
      margin-top: 11px;
      margin-bottom: 25px;
      padding-right: 15px;
      padding-left: 15px;

      font-size: 14px;
      line-height: 17px;
    }
  }

  &__number {
    font-weight: 800;
    font-family: $ff-ttautonomous;
  }

  &__item--big,
  &__item--middle-right,
  &__item--small-right {
    position: relative;

    margin-left: auto;
    padding-right: 36%;

    @include vp-1919 {
      padding-right: 37%;
    }

    @include vp-1439 {
      padding-right: 23%;
    }

    @include vp-639 {
      margin-top: 0;
      padding-right: 15px;
      padding-left: 15px;
    }
  }

  &__item--big {
    padding-top: 7px;

    @include vp-1919 {
      margin-top: -33px;
      padding-top: 0;
    }

    @include vp-1279 {
      padding-top: 54px;
      padding-right: 31%;
    }

    @include vp-1023 {
      padding-top: 74px;
    }

    @include vp-639 {
      padding-top: 0;
    }

    .aurora-main__number {
      font-size: 100px;
      line-height: 140px;

      @include vp-1919 {
        font-size: 70px;
        line-height: 98px;
      }

      @include vp-1279 {
        font-size: 50px;
        line-height: 70px;
      }

      @include vp-639 {
        font-size: 30px;
        line-height: 42px;
      }
    }

    .aurora-main__number-text {
      display: block;
      margin-top: -20px;

      @include vp-1919 {
        margin-top: -10px;
      }

      @include vp-1279 {
        margin-top: -5px;
      }

      @include vp-639 {
        margin-top: 0;
      }
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;

      width: 100%;
      height: 2px;

      background-color: $color-default-white;

      @include vp-1279 {
        top: 52px;
      }

      @include vp-1023 {
        top: 74px;
      }

      @include vp-639 {
        top: 0;
        left: 0;

        width: 150px;
      }
    }
  }

  &__item--middle-left {
    position: relative;
    padding-top: 18px;
    padding-left: 33%;

    @include vp-1919 {
      padding-top: 2px;
      padding-left: 30%;
    }

    @include vp-1439 {
      padding-left: 16%;
    }

    @include vp-1279 {
      padding-left: 19%;
    }

    @include vp-1023 {
      padding-top: 1px;
    }

    @include vp-639 {
      padding-top: 0;
      padding-left: 15px;
    }

    .aurora-main__number-text {
      display: block;
      margin-top: -12px;

      @include vp-1919 {
        margin-top: -6px;
      }

      @include vp-639 {
        margin-top: 0;
      }
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;

      width: 59%;
      height: 2px;

      background-color: $color-default-white;

      @include vp-1919 {
        width: 56%;
      }

      @include vp-1439 {
        width: 44%;
      }

      @include vp-1279 {
        width: 47%;
      }

      @include vp-1023 {
        width: 55%;
      }

      @include vp-639 {
        width: 123px;
      }
    }
  }

  &__item--middle-right {
    margin-bottom: 20px;
    padding-top: 126px;

    @include vp-1919 {
      padding-top: 92px;
      padding-right: 27%;
    }

    @include vp-1439 {
      padding-right: 12%;
    }

    @include vp-1279 {
      padding-top: 71px;
      padding-right: 17%;
    }

    @include vp-1023 {
      padding-right: 15%;
    }

    @include vp-639 {
      padding-top: 0;
    }

    .aurora-main__number-text {
      display: block;
      margin-top: -12px;

      @include vp-1919 {
        margin-top: -6px;
      }

      @include vp-639 {
        margin-top: 0;
      }
    }

    &::before {
      content: "";
      position: absolute;
      top: 107px;
      right: 0;

      width: 100%;
      height: 2px;

      background-color: $color-default-white;

      @include vp-1919 {
        top: 88px;
      }

      @include vp-1279 {
        top: 69px;
      }

      @include vp-639 {
        top: 0;
        left: 0;

        width: 97px;
      }
    }
  }

  &__item--small-right {
    padding-top: 145px;

    @include vp-1919 {
      padding-top: 97px;
      padding-right: 29%;
    }

    @include vp-1439 {
      padding-right: 14%;
    }

    @include vp-1279 {
      padding-top: 78px;
      padding-right: 15%;
    }

    @include vp-639 {
      padding-top: 0;
    }

    .aurora-main__number-text {
      display: block;
      margin-top: -5px;

      @include vp-1919 {
        margin-top: 0;
      }
    }

    &::before {
      content: "";
      position: absolute;
      top: 121px;
      right: 0;

      width: 100%;
      height: 2px;

      background-color: $color-default-white;

      @include vp-1919 {
        top: 85px;
      }

      @include vp-1279 {
        top: 76px;
      }

      @include vp-639 {
        top: 0;
        left: 0;

        width: 135px;
      }
    }
  }

  &__item--small-left {
    position: relative;

    margin-bottom: 20px;
    padding-top: 15px;
    padding-left: 33%;

    @include vp-1919 {
      padding-top: 11px;
      padding-left: 24%;
    }

    @include vp-1439 {
      padding-left: 8%;
    }

    @include vp-1279 {
      padding-top: 3px;
      padding-left: 10%;
    }

    @include vp-1023 {
      padding-top: 2px;
      padding-left: 8%;
    }

    @include vp-639 {
      padding-top: 0;
      padding-left: 15px;
    }

    &::before {
      content: "";
      position: absolute;
      top: -10px;
      left: 0;

      width: 48.5%;
      height: 2px;

      background-color: $color-default-white;

      @include vp-1919 {
        top: 2px;

        width: 42%;
      }

      @include vp-1439 {
        width: 28%;
      }

      @include vp-1279 {
        top: -1px;
      }

      @include vp-1023 {
        width: 32%;
      }

      @include vp-639 {
        width: 110px;
      }
    }
  }

  &__item--middle-right,
  &__item--middle-left {
    .aurora-main__number {
      font-size: 70px;
      line-height: 98px;

      @include vp-1919 {
        font-size: 50px;
        line-height: 70px;
      }

      @include vp-1279 {
        font-size: 40px;
        line-height: 56px;
      }

      @include vp-639 {
        font-size: 30px;
        line-height: 42px;
      }
    }
  }

  &__item--small-left,
  &__item--small-right {
    .aurora-main__number {
      font-size: 50px;
      line-height: 70px;

      @include vp-1919 {
        font-size: 40px;
        line-height: 56px;
      }

      @include vp-1279 {
        font-size: 30px;
        line-height: 42px;
      }
    }
  }

  &__number-text {
    display: block;
    margin: 0;

    font-weight: 500;
    font-size: 20px;
    line-height: 24px;

    @include vp-1919 {
      font-size: 16px;
      line-height: 20px;
    }

    @include vp-1279 {
      font-size: 14px;
      line-height: 17px;
    }
  }
}
