.product-info {
  max-width: 428px;
  padding: 24px 20px;

  display: flex;
  flex-direction: column;

  border: 2px solid $color-default-black;

  @include vp-1919 {
    max-width: 396px;
    padding: 15px 10px 25px;
  }

  @include vp-1439 {
    max-width: 345px;
    padding: 13px 10px;
  }

  @include vp-1023 {
    max-width: 306px;
    padding: 13px 10px;
  }

  @include vp-767 {
    max-width: 100%;
    width: 100%;
    padding: 13px 0;
  }

  &__specs {
    margin-bottom: 28px;

    @include vp-767 {
      margin-bottom: 13px;
    }
  }

  &__price {
    margin-bottom: 28px;

    @include vp-1919 {
      margin-bottom: 40px;
    }

    @include vp-1279 {
      margin-bottom: 30px;
    }

    @include vp-767 {
      margin-bottom: 17px;
    }
  }

  &__btns {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include vp-1279 {
      margin-bottom: 0;
    }

    & .btn-reservation {
      margin: 0;
      min-width: 230px;
      padding: 15px 0;

      font-size: 18px;
      line-height: 16px;
      text-align: center;

      transition: opacity $trans-600;

      @include vp-767 {
        min-width: 180px;
        padding: 12px 0;
        font-size: 14px;
      }

      &:disabled {
        opacity: 0.5;
        pointer-events: none;
      }

      @media (hover: hover), screen and (min-width: 0\0) {
        &:hover,
        &:focus {
          opacity: 0.7;
        }
      }
    }
  }

  &__button-compare {
    min-width: 205px;
    margin-bottom: 15px;
    padding: 0;

    display: flex;
    align-items: center;
    justify-content: space-between;

    font-weight: 600px;
    font-size: 14px;
    line-height: 17px;

    color: $color-zambezi;
    background-color: transparent;
    border: none;
    cursor: pointer;


    transition: color $trans-600;


    @include vp-767 {
      min-width: 180px;

      font-size: 12px;
      line-height: 14px;
    }

    &:disabled {
      opacity: 0.5;
      pointer-events: none;
    }

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        color: $color-alizarin-red;
      }
    }

    svg {
      margin-right: 10px;
    }
  }
}
