.main-content {
  overflow-x: clip;
  border-bottom: 1px solid $color-whisper;
  margin-top: -50px;

  &--mt {
    margin-top: -84px;
  }

  &--no-mt {
    margin-top: 0;
  }

  @include vp-1919 {
    margin-top: -5px;

    &--mt {
      margin-top: 0;
    }

    &--no-mt {
      margin-top: 0;
    }
  }

  @include vp-1439 {
    margin-top: 0;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $color-black-transparent2;
  z-index: 10;
  transition: transform $trans-default;
  transform: scaleY(0);
  transform-origin: 50% 0;

  &--active {
    transform: scaleY(1);
  }

  @include vp-767 {
    display: none;
  }
}

.main-content__wrapper {
  position: relative;
  display: grid;
  grid-template-columns: auto 1fr;
  column-gap: 30px;
  padding: 0 60px 100px;

  @include vp-1919 {
    padding: 0 60px 60px;
  }

  @include vp-1439 {
    padding: 16px 30px 60px;
  }

  @include vp-1279 {
    padding-top: 0;
    display: flex;
    flex-direction: column;
    column-gap: 0;
  }

  @include vp-1023 {
    padding-right: 31px;
    padding-left: 30px;
  }

  @include vp-767 {
    padding-bottom: 30px;
  }
}

.main-content--mt .main-content__wrapper {
  @include vp-1919 {
    padding-top: 16px;
  }

  @include vp-1279 {
    padding-top: 0;
  }
}
