// Футер

// Блок контактных данных
// Общие стили

.footer__contacts {
  background-color: $color-alizarin-red;
}

.footer__contacts-inner {
  display: flex;
  align-items: center;
  min-height: 86px;

  @include vp-1919 {
    min-height: 56px;
  }

  @include vp-1439 {
    padding: 0 60px;
  }

  @include vp-1279 {
    padding: 0 30px;
  }

  @include vp-767 {
    padding: 30px 15px;
    flex-direction: column;
    align-items: flex-start;
    min-height: 227px;
  }
}

.footer__contacts a,
.footer__contacts p,
.footer__contacts span {
  color: $color-default-white;
}

.footer__contacts p {
  margin: 0;
}

// Логотип

.footer__logo {
  margin-right: 200px;

  @include vp-1919 {
    margin-right: 135px;
  }

  @include vp-1279 {
    margin-right: 95px;
  }

  @include vp-1023 {
    margin-right: 45px;
  }

  @include vp-767 {
    margin-right: 0;
    margin-bottom: 20px;
  }
}

// Список данных для связи

.footer__contacts-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;

  @include vp-767 {
    flex-direction: column;
  }
}

.footer__contacts-item {
  margin-right: 60px;

  @include vp-1919 {
    margin-right: 50px;
  }

  @include vp-1279 {
    margin-right: 30px;
  }

  @include vp-1023 {
    margin-right: 0;
  }
}

// Ссылки на соцсети

.footer__socials {
  display: flex;
  align-items: center;
  margin: 5px 0;

  @include vp-1919 {
    margin-left: 15px;
  }

  @include vp-767 {
    margin: 0;
  }
}

// Основной блок подвала

.footer__main-section {
  background-color: $color-night-rider;
}

.footer__main-section-inner {
  display: flex;

  @include vp-1919 {
    flex-direction: column;
  }
}

.footer__main-section-inner p,
.footer__main-section-inner span,
.footer__main-section-inner a {
  color: $color-default-white;
}

// Контакты офисов организации
// Общие стили

.footer__local-office {
  width: 545px;
  padding: 20px 60px 35px 60px;
  box-sizing: border-box;

  @include vp-1919 {
    display: flex;
    width: auto;
    min-height: 90px;
    padding: 12px 60px 11px 60px;
    border-bottom: 2px solid $color-zambezi;
  }

  @include vp-1279 {
    padding: 15px 30px 10px 30px;
  }

  @include vp-1023 {
    min-height: 84px;
  }

  @include vp-767 {
    flex-direction: column;
    padding: 30px 15px 20px 15px;
  }
}

.footer__local-office p {
  margin: 0;
}

// Селект региона

.footer__location {
  margin-bottom: 20px;

  @include vp-1919 {
    margin-right: 150px;
    margin-bottom: 0;
  }

  @include vp-1439 {
    margin-bottom: 15px;
  }

  @include vp-1279 {
    margin-right: 159px;
  }

  @include vp-1023 {
    margin-right: 92px;
    margin-bottom: 10px;
  }

  @include vp-767 {
    margin-bottom: 22px;
    margin-right: 0;
  }
}

// Офис: справочная информация (адрес, место нахождения)

.footer__office-info-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;

  @include vp-767 {
    flex-direction: column;
  }
}

.footer__office-info-item:first-child {
  margin-bottom: 20px;

  @include vp-1919 {
    margin-right: 60px;
    margin-bottom: 0;
    width: 384px;
  }

  @include vp-1279 {
    margin-right: 67px;
  }

  @include vp-1023 {
    width: 300px;
    margin-right: 30px;
  }

  @include vp-767 {
    margin-bottom: 27px;
    margin-right: 0;
  }
}

.footer__info-name {
  font-size: 14px;
  line-height: 17px;
}

.footer__info {
  margin-top: -5px;
  font-size: 18px;
  line-height: 22px;
  font-weight: 600;

  @include vp-1919 {
    font-size: 16px;
    line-height: 20px;
  }

  @include vp-1023 {
    font-size: 14px;
    line-height: 17px;
  }
}

// Меню футера

.footer__nav {
  flex-grow: 1;
}

.footer__nav-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style: none;

  @include vp-1023 {
    grid-template-columns: 1fr 1fr;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
  }
}

.footer__nav-item {
  padding: 30px;
  box-sizing: border-box;
  border-left: 2px solid $color-zambezi;

  @include vp-1919 {
    &:first-of-type {
      padding-left: 60px;
      border-left: none;
    }

    &:last-of-type {
      padding-right: 60px;
    }
  }

  @include vp-1279 {
    &:first-of-type {
      padding-left: 30px;
    }

    &:last-of-type {
      padding-right: 30px;
    }
  }

  @include vp-1023 {
    padding-bottom: 26px;

    &:nth-of-type(odd) {
      border-left: none;
    }

    &:not(:nth-last-of-type(-n + 2)) {
      border-bottom: 2px solid $color-zambezi;
    }
  }

  @include vp-767 {
    padding: 30px 15px 24px 15px;
    border-left: none;

    &:first-of-type {
      padding-left: 15px;
    }

    &:not(:last-of-type) {
      border-bottom: 2px solid $color-zambezi;
    }
  }
}

.footer__sublist {
  margin: 0;
  padding: 0;
  list-style: none;
}

.footer__nav-item-title {
  margin-top: 0;
  margin-bottom: 20px;
  font-family: "TTAutonomous", "Arial", sans-serif;
  font-size: 20px;
  line-height: 28px;
  font-weight: 800;
  color: $color-default-white;
  text-transform: uppercase;

  @include vp-1919 {
    margin-bottom: 15px;
    font-size: 18px;
    line-height: 25px;
  }
}

.footer__sublist-item:not(:last-of-type) {
  margin-bottom: 15px;

  @include vp-1919 {
    margin-bottom: 11px;
  }

  @include vp-1023 {
    margin-bottom: 7px;
  }
}

.footer__sublist--rarefied .footer__sublist-item:not(:last-of-type) {
  margin-bottom: 25px;

  @include vp-1919 {
    margin-bottom: 21px;
  }

  @include vp-1023 {
    margin-bottom: 7px;
  }
}

.footer__sublist-link {
  font-size: 18px;
  line-height: 22px;
  text-decoration: underline;
  transition: color 0.6s ease;

  &:hover,
  &:focus {
    color: $color-alizarin-red;
  }

  &:active {
    color: $color-alizarin-red;
    opacity: 0.8;
  }

  @include vp-1919 {
    font-size: 16px;
    line-height: 20px;
  }

  @include vp-1023 {
    font-size: 14px;
    line-height: 17px;
  }
}

// Правовая информация

.footer__policy {
  background-color: $color-default-black;
}

.footer__policy-inner {
  display: flex;
  align-items: center;
  min-height: 90px;

  @include vp-1919 {
    justify-content: space-between;
    min-height: 74px;
  }

  @include vp-1439 {
    padding: 0 60px;
  }

  @include vp-1279 {
    min-height: 60px;
    padding: 0 30px;
  }

  @include vp-1023 {
    flex-direction: column;
    min-height: 130px;
    padding-top: 15px;
    padding-bottom: 15px;
  }

  @include vp-767 {
    padding: 15px;
  }
}

.footer__copyright,
.footer__privacy-policy,
.footer__developer-slogan,
.footer__developer-link {
  font-size: 14px;
  line-height: 17px;
  font-weight: 500;
  color: $color-default-white;

  @include vp-1279 {
    font-size: 12px;
    line-height: 15px;
  }
}

.footer__copyright {
  width: 214px;
  margin: 0 262px 10px 314px;

  @include vp-1919 {
    margin: 0;
    width: 230px;
  }

  @include vp-1439 {
    margin-right: 125px;
  }

  @include vp-1279 {
    width: 180px;
    margin-right: 80px;
  }

  @include vp-1023 {
    width: auto;
    margin-right: 0;
    margin-bottom: 20px;
  }

  @include vp-767 {
    text-align: center;
  }
}

.footer__privacy-policy,
.footer__developer-link {
  text-decoration: underline;
  margin-right: 100px;
  margin-bottom: 10px;
  transition: color 0.6s ease;

  @include vp-1919 {
    margin-right: 0;
  }

  @include vp-1023 {
    margin-bottom: 20px;
  }

  &:hover {
    color: $color-alizarin-red;
  }

  &:active {
    color: $color-alizarin-red;
    opacity: 0.8;
  }
}

.footer__develop {
  display: flex;
}

.footer__develop-img-container {
  line-height: 0;
  margin-right: 12px;

  @include vp-767 {
    margin-right: 8px;
  }
}

.footer__develop-img-container img {
  width: 99px;
  height: 42px;
  object-fit: contain;
  object-position: right;

  @include vp-1279 {
    width: 70px;
    height: 30px;
  }
}

.footer__developer-slogan {
  margin: 0;
}

.footer__developer-link {
  transition: color 0.6s ease;

  @include vp-1023 {
    display: block;
    margin-bottom: 0;
  }

  &:hover {
    color: $color-alizarin-red;
  }

  &:active {
    color: $color-alizarin-red;
    opacity: 0.8;
  }
}
