.table {
  width: 100%;
  border-collapse: collapse;
}

.table__title {
  margin: 0;
  padding: 0;

  font-weight: 700;
  font-size: 16px;
  line-height: 34px;

  color: $color-default-white;
  background-color: $color-alizarin-red;
  text-align: center;

  @include vp-1439 {
    font-size: 14px;
    line-height: 30px;
  }

  @include vp-767 {
    font-size: 12px;
    line-height: 24px;
  }
}


.table__body-row {
  width: 100%;
  padding: 0 20px;

  display: flex;
  justify-content: space-between;

  background-color: $color-white-smoke;

  &:nth-child(even) {
    background-color: $color-default-white;
  }

  @include vp-1439 {
    padding: 3px 10px;
  }

  @include vp-1023 {
    padding: 3px 5px;
  }
}

.table__item-name,
.table__item-value {
  font-size: 16px;
  line-height: 32px;
  color: $color-vulcan;

  @include vp-1439 {
    font-size: 14px;
    line-height: 30px;
  }

  @include vp-1279 {
    font-size: 14px;
    line-height: 22px;
  }

  @include vp-767 {
    font-size: 12px;
    line-height: 16px;
  }
}

.table__item-value {
  font-weight: 700;
  max-width: 250px;
  text-align: right;
}
