// Хэдер
// Общие стили

.header {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 500;
  display: flex;
  min-height: 64px;
  border-bottom: 4px solid $color-alizarin-red;
  background-color: $color-default-black;

  @include vp-1919 {
    min-height: 54px;
  }
}

// Общий контейнер

.header__inner {
  display: flex;
  align-items: center;
  align-self: stretch;
}

// Логотип

.header__logo {
  margin-right: 20px;
  margin-left: 20px;

  @include vp-1919 {
    margin-right: 30px;
    margin-left: 15px;
  }

  @include vp-1439 {
    margin-right: 28px;
  }

  @include vp-1279 {
    margin-right: auto;
  }
}

// Кнопка переключения меню (на планшете и мобильном)

.header__nav-toggle {
  display: none;

  @include vp-1279 {
    align-self: stretch;
    display: block;
    width: 50px;
    padding: 0;
    border: none;
    outline: none;
    background-color: $color-default-white;
    cursor: pointer;
  }
}

.header__nav-toggle svg {
  width: 32px;
  height: 16px;
  color: $color-alizarin-red;
}

// Главное меню (контейнер)

.header__nav {
  max-width: 875px;
  margin-left: auto;
  margin-right: 44px;

  @include vp-1919 {
    max-width: 810px;
    margin-left: 0;
    margin-right: auto;
  }

  @include vp-1439 {
    max-width: 687px;
  }

  @include vp-1279 {
    max-width: none;
  }
}

// Элементы взаимодействия - общие стили

.header__controls {
  align-self: stretch;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;

  @include vp-767 {
    display: none;
  }
}

// Форма поиска

.header__search {
  position: relative;
  align-self: stretch;
  display: flex;
  align-items: center;
  width: 128px;
  min-height: 60px;
  padding: 5px 20px;
  background-color: $color-default-white;
  border: none;
  border-left: 1px solid $color-night-rider;
  outline: none;
  box-sizing: border-box;
  font-size: 12px;
  line-height: 15px;
  font-weight: 600;
  text-align: left;
  color: $color-zambezi;
  cursor: pointer;

  @include vp-1919 {
    justify-content: center;
    width: 50px;
    min-height: 50px;
    padding: 0;
    font-size: 0;
  }

  @include vp-767 {
    display: none;
  }
}

.header__search svg {
  margin-right: 10px;

  @include vp-1919 {
    margin-right: 0;
    width: 20px;
    height: 20px;
  }
}

// Кнопка меню обратной связи

.header__callback {
  position: relative;
  align-self: stretch;
  display: flex;
  align-items: center;
  max-width: 138px;
  padding: 15px 20px;
  background-color: $color-alizarin-red;
  border: none;
  outline: none;
  box-sizing: border-box;
  font-size: 12px;
  line-height: 15px;
  font-weight: 600;
  text-align: left;
  color: $color-default-white;
  cursor: pointer;
  transition: opacity 0.6s ease;

  &:hover,
  &:focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.7;
  }

  @include vp-1919 {
    padding: 10px 15px;
    max-width: 128px;
  }

  @include vp-1439 {
    max-width: 116px;
    font-size: 10px;
    line-height: 12px;
  }
}

.header__callback svg {
  width: 40px;
  height: 25px;
  margin-right: 8px;
}
