.link-download {
  position: relative;

  align-items: center;
  width: max-content;

  font-weight: 700;
  font-size: 16px;
  line-height: 20px;

  transition: color $trans-600;

  @include vp-1279 {
    font-size: 14px;
    line-height: 18px;
  }

  span {
    display: block;
  }

  svg {
    position: absolute;
    top: 7px;
    left: -35px;

    width: 27px;
    height: 27px;

    color: $color-alizarin-red;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      color: $color-alizarin-red;
    }
  }
}
