.socials p {
  margin-top: 3px;
  margin-bottom: 11px;
  font-size: 14px;
  line-height: 17px;

  @include vp-1919 {
    margin-bottom: 7px;
    font-size: 12px;
    line-height: 15px;
  }

  @include vp-1023 {
    margin-bottom: 8px;
    font-size: 10px;
    line-height: 12px;
  }
}

.socials__list {
  display: flex;
  flex-wrap: wrap;
  row-gap: 10px;
  max-width: 400px;
  padding: 0;
  list-style: none;
  font-size: 0;
  line-height: 0;

  @include vp-1439 {
    max-width: 250px;
  }

  @include vp-1023 {
    max-width: 170px;
  }
}

.socials__item {
  margin-right: 10px;
}

.socials__icon {
  fill: $color-default-white;
  transition: fill 0.6s ease;
}

.socials__link {
  &:hover svg {
    fill: $color-aluminum;
  }

  &:active svg {
    fill: $color-zambezi;
  }

  &--youtube svg {
    width: 24px;
    height: 17px;

    @include vp-1919 {
      width: 19px;
      height: 14px;
    }

    @include vp-1023 {
      width: 17px;
      height: 12px;
    }
  }
}
