.hotline__tabs-list {
  margin: 0;
  padding: 0;

  display: flex;
  list-style: none;

  @include vp-374 {
    display: flex;
    justify-content: space-between;
  }
}

.hotline__tabs-item:not(:last-child) {
  margin: 0 10px 0 0;
}

.hotline__tabs-button {
  position: relative;
  padding: 8px 20px;

  font-weight: 700;
  font-size: 16px;
  line-height: 24px;

  background: $color-whisper;
  color: $color-zambezi;
  border: none;

  cursor: pointer;
  transition: background-color $trans-600, color $trans-600;

  @include vp-767 {
    font-size: 14px;
    line-height: 16px;
  }

  @include vp-374 {
    font-size: 12px;
    line-height: 16px;
  }

  &--active {
    background: rgba(0, 0, 0, 0.4);
    color: $color-default-white;

    transition: background-color $trans-600, color $trans-600;
  }
}

.hotline__content {
  max-width: 710px;
  display: none;

  &--active {
    display: block;
  }
}

.hotline__form {
  padding: 27px 20px;

  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 30px;

  border: 2px solid $color-whisper;
  background-color: $color-default-white;

  @include vp-767 {
    padding: 15px;
    display: block;
  }

  textarea {
    grid-column-start: 1;
    grid-column-end: 3;
    width: 100%;
    max-width: 405px;
    max-height: 120px;
    height: 120px;
    margin-bottom: 15px;
    padding: 12px 15px;

    font-weight: 500;
    font-size: 16px;
    line-height: 16px;
    border: 1px solid $color-whisper;
    transition: opacity $trans-600, background-color $trans-600, border-color $trans-600;

    @include vp-1023 {
      padding: 10px;
      font-size: 14px;
      line-height: 14px;
    }

    @include vp-767 {
      max-width: 100%;
      padding: 10px;
    }

    &::placeholder {
      font-weight: 600;
      color: $color-dark-gray;
    }

    &:hover {
      cursor: pointer;
      opacity: 0.8;
      background-color: $color-white-smoke;
      transition: opacity $trans-600, background-color $trans-600;
    }

    &:focus {
      border-color: $color-vulcan;
      outline: 0;
      transition: border-color $trans-600;
    }
  }
}

.hotline__form-accept {
  position: relative;
  grid-column-start: 1;
  grid-column-end: 3;
  width: 100%;
  padding: 0;

  & label {
    position: relative;
    padding: 0 0 0 18px;

    font-weight: 500;
    font-size: 14px;
    line-height: 17px;

    color: $color-vulcan;
    border: none;
    height: auto;

    cursor: pointer;

    @include vp-767 {
      font-size: 10px;
      line-height: 14px;
    }

    & span {
      position: absolute;
      left: 0;
      top: 2px;
      width: 12px;
      height: 12px;
      border: 1px solid $color-alizarin-red;
      background-color: $color-default-white;
      box-sizing: border-box;

      @include vp-767 {
        top: 0;
      }

      &::before {
        position: absolute;
        top: -6px;
        left: 1px;
        content: url("../img/sprite/icon-check.svg");
        width: 11px;
        height: 14px;
        opacity: 0;
        transition: opacity $trans-default;
      }
    }

    & input:checked + span {
      border: 1px solid $color-alizarin-red;

      &::before {
        background-color: transparent;
        opacity: 1;
        transition: opacity $trans-default;
      }
    }
  }

  &.is-invalid label {
    border-bottom: 2px solid $color-alizarin-red;
  }
}

.hotline__form-button {
  width: 151px;
  margin: 20px 0 0 0;
  padding: 12px;

  display: flex;
  justify-content: center;

  font-weight: 700;
  font-size: 14px;
  line-height: 16px;

  &:focus {
    box-shadow: 0 0 0 1px $color-vulcan;
    outline: 0;
  }
}
