// Colors
// ---------------------------------

// Default

$color-default-black:               #000000;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);

// Project palette
// для нейминга цветов используем https://www.htmlcsscolor.com/hex/334482

$color-venetian-red:                #b01319;
$color-venetian-red2:               #cb0007;
$color-torch-red:                   #f11820;
$color-alizarin-red:                #e21e25;
$color-night-rider:                 #333333;
$color-vulcan:                      #343741;
$color-zambezi:                     #585858;
$color-dim-gray:                    #666666;
$color-aluminum:                    #a7a5a6;
$color-dark-gray:                   #aaaaaa;
$color-silver:                      #bfbfbf;
$color-whisper:                     #eaeaea;
$color-white-smoke:                 #f3f3f3;
$color-snow:                        #f9f9f9;

$color-black-transparent4:          rgba(0, 0, 0, 0.9);
$color-black-transparent2:          rgba(0, 0, 0, 0.6);
$color-black-transparent3:          rgba(0, 0, 0, 0.4);
$color-black-transparent:           rgba(0, 0, 0, 0.15);

// Gradient

$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);
$black-to-right2:                   linear-gradient(180deg, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0.6) 100%);
$black-to-right3:                   linear-gradient(180deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.2) 100%);
$black-to-right4:                   linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
$red-to-right:                      linear-gradient(223.06deg, $color-torch-red 0%, $color-venetian-red2 100%);
$white-to-right:                    linear-gradient(223.06deg, $color-default-white 0%, $color-whisper 100%);

// Gradient
$border-grey:                       2px solid $color-whisper;
$border-red:                        2px solid  $color-alizarin-red;
// Typography
// ---------------------------------

// переменная используемая в html для подключения скейлинга
$fz-default:                        16px;

// у некоторых шрифтов в ios возникают проблемы с символом рубля
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-rouble:                         "Rouble", "Arial", sans-serif;

$ff-montserrat:                    "Montserrat", "Arial", sans-serif;
$ff-ttautonomous:                  "TTAutonomous", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

// если требуется изинг отличный от $tf-default, то переменную называем не цифрой
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-375:                            375px;
$vp-640:                            640px;
$vp-768:                            768px;
$vp-1024:                           1024px;
$vp-1280:                           1280px;
$vp-1440:                           1440px;
$vp-1711:                           1711px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;
