.pagination-numb {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;

  width: max-content;

  &__prev,
  &__next {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    width: 40px;
    height: 20px;
    border: none;
    cursor: pointer;
    color: $color-default-black;
    background-color: $color-default-white;
    transition: color $trans-default;

    svg {
      width: 40px;
      height: 18px;

      @include vp-767 {
        width: 25px;
        height: 17px;
      }
    }

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        color: $color-alizarin-red;
      }
    }

    @include vp-767 {
      width: 25px;
      height: 15px;
    }
  }

  &__prev {
    margin-right: 20px;
    transform: rotate(180deg);
  }

  &__next {
    margin-left: 10px;
  }

  &__btn {
    margin-right: 10px;

    width: 30px;
    height: 30px;
    padding: 0;

    text-align: center;
    font-weight: 500;

    cursor: pointer;
    border: 2px solid $color-default-black;
    background-color: $color-default-white;
    color: $color-default-black;
    transition: color $trans-default, border-color $trans-default, background-color $trans-default;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        border-color: $color-alizarin-red;
        color: $color-alizarin-red;
      }
    }

    &--more {
      border: none;
    }

    &.is-active {
      color: $color-default-white;
      background-color: $color-alizarin-red;
      border-color: $color-alizarin-red;

      @media (hover: hover), screen and (min-width: 0\0) {
        &:hover,
        &:focus {
          color: $color-default-white;
          border-color: $color-venetian-red;
          background-color: $color-venetian-red;
        }
      }
    }
  }
}
