// Мобильное меню
// Общие стили

.main-nav {
  @include vp-1279 {
    display: none;
    opacity: 0;
  }
}

@keyframes ani {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.main-nav.is-active {
  @include vp-1279 {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    display: block;
    width: 100vw;
    height: 100vh;
    background-color: rgba($color: $color-default-black, $alpha: 0.5);
    overflow: auto;
    animation: ani 0.5s forwards;
  }
}

.main-nav__wrapper {
  @include vp-1279 {
    position: relative;
    width: 280px;
    min-height: 100vh;
    background-color: $color-default-white;
  }
}

.main-nav__inner[data-phone-controls],
.main-nav__inner[data-user-controls] {
  display: none;

  @include vp-1279 {
    display: flex;
  }
}

.main-nav__inner {
  @include vp-1279 {
    display: flex;
    flex-direction: column;
    padding: 20px;
    border-bottom: 1px solid $color-whisper;

    &:first-of-type {
      padding-top: 35px;
    }

    &:last-of-type {
      padding-bottom: 40px;
      border-bottom: none;
    }
  }
}

// Копка закрытия мобильного меню

.main-nav__close {
  display: none;

  @include vp-1279 {
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    width: 30px;
    height: 30px;
    padding: 0;
    border: none;
    outline: none;
    box-sizing: border-box;
    background-color: $color-alizarin-red;
    cursor: pointer;
    transition: opacity 0.6s ease;

    &:hover,
    &:focus {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.7;
    }
  }
}

.main-nav__close svg {
  width: 11px;
  height: 11px;
  fill: $color-default-white;
}

// Меню

.main-nav__link,
.main-nav__dropdown-link {
  font-size: 15px;
  line-height: 18px;
  font-weight: 700;
  color: $color-default-white;

  @include vp-1919 {
    font-size: 14px;
    line-height: 17px;
  }

  @include vp-1439 {
    font-size: 12px;
    line-height: 15px;
  }
}

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;

  @include vp-1279 {
    display: flex;
    flex-direction: column;
  }
}

.main-nav__item {
  position: relative;

  &:not(:last-of-type) {
    margin-right: 24px;

    @include vp-1919 {
      margin-right: 20px;
    }

    @include vp-1439 {
      margin-right: 15px;
    }

    @include vp-1279 {
      margin-right: 0;
      margin-bottom: 6px;
    }
  }
}

.main-nav__link {
  color: $color-default-white;
  transition: color 0.6s ease;

  @include vp-1279 {
    color: $color-default-black;
  }
}

.main-nav__link:hover {
  color: $color-alizarin-red;
}

.main-nav__link:active {
  color: $color-alizarin-red;
  opacity: 0.8;
}

.main-nav__item--active .main-nav__link {
  color: $color-alizarin-red;
}

// Выпадающее меню

.main-nav__dropdown {
  z-index: 105;
}

.main-nav__dropdown-list {
  position: absolute;
  top: 35px;
  display: flex;
  flex-direction: column;
  min-width: 300px;
  padding: 15px;
  background-color: $color-night-rider;
  list-style: none;

  @include vp-1279 {
    position: relative;
    top: auto;
    min-width: auto;
    padding: 0 0 0 15px;
    background-color: transparent;
  }
}

.main-nav__dropdown-link {
  transition: color 0.6s ease;

  @include vp-1279 {
    color: $color-zambezi;
  }
}

.main-nav__dropdown-item:hover .main-nav__dropdown-link {
  color: $color-alizarin-red;
}

.main-nav__dropdown-item:active .main-nav__dropdown-link {
  color: $color-alizarin-red;
  opacity: 0.8;
}

// Конпка открытия/закрытия выпадающего меню

.main-nav__dropdown-toggle {
  width: 12px;
  height: 7px;
  padding: 0;
  background-color: transparent;
  border: none;
  outline: none;
  line-height: 0;
  color: $color-alizarin-red;
  cursor: pointer;
}

.main-nav__dropdown-toggle > .is-open {
  transform: rotate(180deg);
}

.main-nav__dropdown-item:not(:last-of-type) {
  margin-bottom: 10px;
}

.main-nav__dropdown-toggle svg {
  width: 12px;
  height: 7px;
}

// Обратный звонок

.main-nav__callback {
  @include vp-1279 {
    position: relative;
    display: flex;
    align-items: center;
    width: 164px;
    min-height: 30px;
    padding: 3px 5px 3px 39px;
    background-color: $color-alizarin-red;
    border: none;
    outline: none;
    font-size: 12px;
    line-height: 15px;
    font-weight: 600;
    color: $color-default-white;
    transition: opacity 0.6s ease;

    &::before {
      content: "";
      position: absolute;
      top: 5px;
      left: 10px;
      display: block;
      width: 18px;
      height: 20px;
      background: url("../img/svg/icon-callback.svg") no-repeat center center;
      background-color: transparent;
    }

    &:hover,
    &:focus {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.7;
    }
  }
}

// Форма поиска

.main-nav__search-form form {
  @include vp-1279 {
    position: relative;
    display: flex;
    align-items: stretch;
    justify-content: stretch;
    min-height: 44px;
  }
}

.main-nav__search-form svg {
  width: 24px;
  height: 23px;

  @include vp-1279 {
    position: absolute;
    top: 10px;
    left: 10px;
    fill: $color-alizarin-red;
  }
}

.main-nav__search-form input {
  @include vp-1279 {
    width: 100%;
    padding: 0 10px 0 42px;
    background-color: transparent;
    border: 1px solid $color-whisper;
    outline: none;
    font-size: 12px;
    line-height: 15px;
    font-weight: 600;

    &::placeholder {
      font-size: 12px;
      line-height: 15px;
      font-weight: 600;
      color: $color-zambezi;
    }
  }
}
