.main-header--catalog {
  border: none;
}

.main-header--catalog .container {
  padding-bottom: 0;

  @include vp-1919 {
    padding-bottom: 18px;
  }

  @include vp-1439 {
    padding-bottom: 15px;
  }

  @include vp-1023 {
    padding-bottom: 15px;
  }

  @include vp-767 {
    padding-top: 16px;
    padding-bottom: 15px;
  }
}

.main-header--catalog .breadcrumbs {
  @include vp-1919 {
    margin-bottom: 30px;
  }

  @include vp-1439 {
    margin-bottom: 31px;
  }

  @include vp-767 {
    margin-bottom: 11px;
  }
}

.main-header--catalog .breadcrumbs__item:not(:last-child) {
  @include vp-1023 {
    margin-right: 40px;
  }
}

.main-header--catalog .main-header__title {
  @include vp-1919 {
    margin-bottom: 15px;
  }

  @include vp-767 {
    font-size: 24px;
    line-height: 32px;
    margin-bottom: 11px;
  }
}

.main-header--catalog .main-header__description {
  @include vp-1919 {
    margin-bottom: 15px;
  }

  @include vp-1023 {
    font-size: 16px;
    line-height: 20px;
    margin-bottom: 14px;
  }
}

.main-header--catalog .main-header__link {
  @include vp-1919 {
    margin-left: -1px;
  }
}
