.form-upload-field {
  position: relative;
  min-height: 40px;
  height: auto;

  align-items: center;
  flex-direction: row;

  border: 1px solid $color-whisper;
  transition: background-color $trans-600;

  &:hover {
    background-color: $color-white-smoke;
  }

  & label {
    position: static;
    width: auto;
    height: auto;
    margin: 0 8px 0 auto;
    padding: 0 12px 0;

    font-weight: 700;
    font-size: 12px;
    line-height: 24px;

    align-items: center;
    border: 2px solid $color-zambezi;
    text-transform: uppercase;
    cursor: pointer;

    transition: color $trans-600;

    &:hover {
      color: $color-dark-gray;
      transition: color $trans-600;
    }

    &:focus {
      border: 1px solid $color-dark-gray;
    }

    @include vp-1279 {
      margin: 0 6px 0 auto;
    }

    @include vp-374 {
      border: 1px solid $color-zambezi;
    }
  }

  & input {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
  }
}

.form-upload-field__preview {
  position: absolute;
  max-width: 230px;
  left: 16px;

  font-size: 16px;
  line-height: 16px;
  font-weight: 600;
  color: $color-dark-gray;

  @include vp-1023 {
    font-size: 14px;
    line-height: 14px;
  }

  @include vp-374 {
    font-size: 12px;
    line-height: 12px;
  }

  & ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  & p {
    margin: 0 0 0 0;
  }
}
