.promo {
  position: relative;

  width: 100%;
  margin: 0 auto;
  margin-bottom: 79px;
  overflow: hidden;

  @include vp-1919 {
    margin-bottom: 57px;
  }

  @include vp-1279 {
    margin-bottom: 42px;
  }

  @include vp-1023 {
    margin-bottom: 70px;
  }

  @include vp-767 {
    margin-bottom: 30px;
  }

  &__slider {
    overflow: hidden;

    background-color: $color-night-rider;
  }

  &__slides {
    display: flex;
  }

  &__slide {
    display: flex;
    flex: 0 0 100%;
  }

  &__slide-img {
    height: 780px;

    @include vp-1919 {
      height: 650px;
    }

    @include vp-1439 {
      height: 578px;
    }

    @include vp-1279 {
      height: 446px;
    }

    @include vp-767 {
      height: 301px;
    }

    img {
      position: absolute;
      top: 50%;

      width: 100%;
      height: 100%;
      transform: translateY(-50%);

      object-fit: cover;
    }
  }

  &__slide-icon {
    position: absolute;
    top: 18px;
    right: 100px;

    width: 550px;
    height: 550px;

    @include vp-1919 {
      top: 23px;
      right: 171px;

      width: 409px;
      height: 430px;
    }

    @include vp-1439 {
      width: 339px;
      height: 360px;
    }

    @include vp-1279 {
      right: 140px;

      width: 286px;
      height: 300px;
    }

    @include vp-1023 {
      top: 40px;
      right: -60px;
    }

    @include vp-767 {
      display: none;
    }

    img {
      position: absolute;

      width: 100%;

      object-fit: contain;
    }
  }

  &__slide-inner {
    position: relative;

    display: flex;
  }

  &__slide-content {
    position: relative;

    width: 33%;
    margin-top: 176px;
    margin-left: 300px;
    padding-bottom: 125px;

    color: $color-default-white;

    @include vp-1919 {
      width: 39%;
      margin: 124px auto 0 30px;
    }

    @include vp-1439 {
      width: 41%;
      margin-top: 100px;
    }

    @include vp-1279 {
      width: 44%;
      margin-top: 60px;
      margin-left: 0;
    }

    @include vp-1023 {
      width: 63%;
    }

    @include vp-767 {
      width: 100%;
      margin-top: 30px;
      padding-bottom: 20px;
    }

    .title--h3 {
      margin-bottom: 15px;

      @include vp-1919 {
        margin-bottom: 9px;
      }

      @include vp-767 {
        margin-bottom: 15px;
      }
    }

    .btn {
      padding: 14px 30px;

      font-weight: 700;
      font-size: 18px;
      line-height: 16px;
      text-transform: uppercase;

      background-color: $color-alizarin-red;

      @include vp-1279 {
        padding: 9px 14px;

        font-size: 14px;
      }

      @include vp-767 {
        padding: 5px 10px;

        font-size: 12px;
      }

      svg {
        width: 40px;
        height: 18px;
        margin-left: 10px;

        @include vp-1279 {
          width: 30px;
          margin-left: 13px;
        }

        @include vp-767 {
          width: 23px;
        }
      }
    }
  }

  &__slide-description {
    margin: 0;
    margin-bottom: 40px;

    font-weight: 800;
    font-size: 30px;
    line-height: 45px;
    font-family: $ff-ttautonomous;
    text-transform: uppercase;

    @include vp-1919 {
      margin-bottom: 30px;

      font-size: 24px;
      line-height: 30px;
    }

    @include vp-1279 {
      margin-bottom: 22px;

      font-size: 20px;
      line-height: 24px;
    }

    @include vp-767 {
      margin-bottom: 15px;

      font-size: 16px;
      line-height: 20px;
    }
  }

  &__slide-text {
    margin: 0;
    margin-bottom: 30px;

    font-weight: 600;
    font-size: 28px;
    line-height: 36px;

    @include vp-1919 {
      max-width: 415px;
      margin-bottom: 20px;

      font-size: 20px;
      line-height: 28px;
    }

    @include vp-1279 {
      max-width: 370px;
      margin-bottom: 10px;

      font-size: 16px;
      line-height: 20px;
    }

    @include vp-767 {
      margin-bottom: 5px;

      font-size: 14px;
      line-height: 18px;
    }
  }

  &__pagination-wrapper {
    position: relative;
  }

  .swiper-pagination-horizontal {
    bottom: 55px;

    padding-left: 320px;

    @include vp-1919 {
      bottom: 90px;

      padding-left: 60px;
    }

    @include vp-1439 {
      bottom: 60px;
    }

    @include vp-1279 {
      padding-left: 30px;
    }

    @include vp-1023 {
      padding-left: 25px;
    }

    @include vp-767 {
      bottom: 85px;

      padding-left: 15px;
    }

    @include vp-639 {
      bottom: 30px;
    }
  }

  &__pagination {
    position: absolute;

    &::before,
    &::after {
      content: "";
      position: absolute;

      height: 2px;

      background-color: $color-default-white;
    }

    &::before {
      right: 0;

      width: 1470px;

      @include vp-1919 {
        width: calc(100% - 190px);
      }

      @include vp-1279 {
        width: calc(100% - 130px);
      }

      @include vp-1023 {
        width: calc(100% - 126px);
      }

      @include vp-767 {
        width: calc(100% - 115px);
      }
    }

    &::after {
      left: 0;

      width: 300px;

      @include vp-1919 {
        width: calc(10px + 2%);
      }

      @include vp-1439 {
        width: calc(13px + 2%);
      }

      @include vp-1279 {
        width: calc(3px + 2%);
      }

      @include vp-1023 {
        width: calc(6px + 1%);
      }

      @include vp-767 {
        width: calc(2px + 1%);
      }
    }
  }

  &__cards {
    display: flex;
    justify-content: flex-end;
    width: calc(100% - 8px);
    margin: -282px 0 33px 0;
    padding: 0;

    list-style: none;

    @include vp-1919 {
      width: calc(100% + 15px);
      margin-top: -235px;
    }

    @include vp-1439 {
      width: calc(100% - 15px);
      margin-top: -225px;
    }

    @include vp-1279 {
      width: calc(100% + 12px);
      margin: -144px 0 17px 0;
    }

    @include vp-1023 {
      justify-content: flex-start;
      margin: -32px 0 17px -9px;
    }

    @include vp-767 {
      justify-content: center;
      width: 100%;
      margin: -30px auto 17px;
    }

    @include vp-639 {
      flex-wrap: wrap;
      justify-content: flex-start;
      margin: 29px auto 15px;
    }
  }

  .card-text {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 406px;
    min-height: 350px;
    margin: 0 15px 0;
    padding: 20px;

    filter: drop-shadow(4px 4px 0 rgba(0, 0, 0, 0.25));
    transform: translate3d(0, 0, 0);

    transition: opacity $trans-600;

    @include vp-1919 {
      max-width: 320px;
      min-height: 284px;
      padding: 15px;
    }

    @include vp-1279 {
      max-width: 290px;
      min-height: 255px;
    }

    @include vp-767 {
      min-height: 240px;
    }

    @include vp-639 {
      max-width: 100%;
      margin: 0 0 16px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &--red {
      color: $color-default-white;

      .link {
        transition: color $trans-600;

        &--white {
          @media (hover: hover), screen and (min-width: 0\0) {
            &:hover,
            &:focus {
              color: $color-dark-gray;
            }
          }
        }
      }
    }

    &--grey {
      .card-text__info {
        color: $color-dark-gray;
      }

      .card-text__text {
        color: $color-zambezi;
      }
    }

    &__info {
      margin-bottom: 20px;
      margin-left: auto;

      font-weight: 600;
      font-size: 14px;
      line-height: 17px;
      text-transform: uppercase;

      @include vp-1919 {
        margin-bottom: 15px;
      }

      @include vp-1279 {
        font-size: 12px;
        line-height: 15px;
      }
    }

    &__inner {
      display: flex;
      flex-direction: column;
      height: 100%;
    }

    &__content-wrapper {
      display: flex;
      flex-direction: column;
      height: inherit;
    }

    &__date {
      margin-top: 10px;

      font-weight: 700;
      font-size: 16px;
      line-height: 20px;
      color: $color-dark-gray;

      @include vp-1919 {
        margin-top: 22px;

        font-size: 14px;
        line-height: 17px;
      }

      @include vp-1279 {
        margin-top: 13px;

        font-size: 12px;
        line-height: 15px;
      }
    }

    &__text {
      flex: 1 1 auto;
      max-width: 345px;
      margin: 0;
      margin-top: 20px;

      font-size: 18px;
      line-height: 22px;

      @include vp-1919 {
        margin-top: 13px;

        font-size: 16px;
        line-height: 20px;
      }

      @include vp-1279 {
        max-width: 250px;
        margin-top: 15px;

        font-size: 14px;
        line-height: 17px;
      }

      @include vp-639 {
        max-width: 87%;
      }
    }

    &__text-info {
      font-weight: 700;
    }

    .link {
      margin-left: auto;

      @include vp-1919 {
        font-size: 14px;
        line-height: 17px;
      }

      svg {
        width: 34px;
        height: 15px;
        margin-left: 5px;
      }
    }
  }

  &__link {
    margin-right: 22px;

    text-align: right;
    text-transform: uppercase;

    @include vp-1919 {
      margin-right: 0;

      font-size: 18px;
      line-height: 22px;
    }

    @include vp-1439 {
      margin-right: 30px;
    }

    @include vp-1279 {
      margin-right: 0;

      font-size: 14px;
      line-height: 17px;
    }

    @include vp-1023 {
      width: 100%;
      max-width: 619px;

      text-align: right;
    }

    @include vp-767 {
      width: 100%;
      max-width: 100%;

      text-align: right;
    }

    .link {
      font-weight: 700;
      font-size: 20px;
      line-height: 24px;

      @include vp-1919 {
        font-size: 18px;
        line-height: 22px;
      }

      @include vp-1279 {
        font-size: 14px;
        line-height: 17px;
      }
    }

    svg {
      width: 40px;
      height: 18px;
      margin-left: 10px;
    }
  }

  &__block {
    display: flex;
    flex-direction: column;
  }
}
