.project-geography {
  position: relative;

  display: flex;
  flex-direction: column;
  margin-bottom: 97px;
  padding-bottom: 88px;

  color: $color-default-white;

  background: linear-gradient(266.2deg, rgba(0, 0, 0, 0.8) 3.38%, #000000 96.89%);

  @include vp-1919 {
    margin-bottom: 63px;
    padding-bottom: 82px;
  }

  @include vp-1439 {
    padding-bottom: 30px;
  }

  @include vp-1279 {
    margin-bottom: 35px;
    padding-bottom: 0;

    background: none;
  }

  @include vp-1023 {
    margin-bottom: 53px;
  }

  @include vp-639 {
    margin-bottom: 60px;
  }

  &__wrapper {
    @include vp-1279 {
      min-height: 800px;

      background: linear-gradient(266.2deg, rgba(0, 0, 0, 0.8) 3.38%, #000000 96.89%);

      @include vp-1023 {
        min-height: 700px;
      }

      @include vp-767 {
        min-height: 480px;
      }

      @include vp-639 {
        display: flex;
        flex-direction: column;
      }
    }
  }

  &__wrapper-slider {
    @include vp-1439 {
      display: flex;
      flex-direction: column;
    }

    @include vp-1279 {
      margin-top: -257px;
    }

    @include vp-1023 {
      margin-top: -244px;
    }

    @include vp-767 {
      margin-top: -110px;
    }
  }

  &__img {
    position: absolute;

    width: 100%;
    max-width: 1257px;
    height: 703px;
    margin-left: 134px;

    @include vp-1919 {
      max-width: 1000px;
      height: 558px;
      margin-left: 41px;
    }

    @include vp-1439 {
      max-width: 900px;
      height: 503px;
      margin-left: 30px;
    }

    @include vp-1279 {
      margin-left: 54px;
    }

    @include vp-1023 {
      max-width: 707px;
      height: 395px;
      margin-left: 29px;
    }

    @include vp-767 {
      max-width: 760px;
      height: 168px;
      margin-left: 0;
    }

    @include vp-639 {
      order: 2;
    }

    img {
      position: absolute;

      width: 100%;
      margin-top: -112px;

      @include vp-1279 {
        margin-top: 10px;
      }

      @include vp-1023 {
        margin-top: 30px;
      }

      @include vp-639 {
        position: relative;

        margin-top: 162px;
      }
      object-fit: contain;
    }
  }

  &__text-content {
    position: relative;

    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-top: 75px;
    margin-right: 60px;
    margin-left: auto;

    @include vp-1919 {
      margin-top: 55px;
    }

    @include vp-1439 {
      margin-bottom: 137px;
    }

    @include vp-1279 {
      align-items: flex-start;
      margin-top: 32px;
      margin-left: 0;
      padding-left: 30px;
    }

    @include vp-767 {
      margin-right: 0;
      padding-right: 15px;
      padding-left: 15px;
    }

    @include vp-639 {
      margin-bottom: 0;
    }

    .title--h1 {
      margin-bottom: 30px;

      text-align: right;

      @include vp-1919 {
        margin-bottom: 10px;
      }

      @include vp-1279 {
        margin-bottom: 21px;

        text-align: left;
      }

      @include vp-767 {
        margin-bottom: 9px;
      }
    }
  }

  &__text {
    width: 100%;
    max-width: 390px;
    margin: 0;
    margin-bottom: 20px;

    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    text-align: right;

    @include vp-1919 {
      max-width: 375px;

      font-size: 16px;
      line-height: 20px;
    }

    @include vp-1279 {
      max-width: 328px;

      font-size: 14px;
      line-height: 17px;
      text-align: left;
    }

    @include vp-767 {
      max-width: 100%;
    }
  }

  &__slider {
    position: relative;

    display: flex;
    margin-top: 316px;
    margin-left: 20px;
    overflow: hidden;

    @include vp-1919 {
      max-width: 73%;
      margin-top: 209px;
      margin-left: 15px;
    }

    @include vp-1439 {
      order: 2;
      max-width: 85%;
      margin-top: 0;
    }

    @include vp-1279 {
      max-width: 100%;
      margin-top: 0;
      margin-right: 15px;
    }

    @include vp-1023 {
      margin-right: 0;
    }

    @include vp-767 {
      margin-left: 0;
      padding-bottom: 50px;
      padding-left: 7.5px;
    }
  }

  &__cards {
    position: relative;
    z-index: 1;

    display: flex;
    flex-wrap: wrap;
    margin: 0 0 59px;
    padding: 0;

    list-style: none;

    @include vp-1439 {
      margin-bottom: 0;
    }

    @include vp-1023 {
      flex-wrap: nowrap;
    }
  }

  .card {
    position: relative;
    z-index: 2;

    width: 100%;
    max-width: 406px;
    margin-right: 20px;
    margin-bottom: 30px;
    margin-left: 20px;

    background: $white-to-right;

    @include vp-1919 {
      max-width: 320px;
      margin-right: 15px;
      margin-left: 15px;
    }

    @include vp-1279 {
      max-width: 300px;
    }

    @include vp-1023 {
      width: 300px;
      margin-bottom: 0;
    }

    @include vp-767 {
      margin-right: 7.5px;
      margin-left: 7.5px;
    }

    @include vp-639 {
      max-width: 290px;
      overflow: hidden;
    }

    &__img-container {
      height: 240px;

      @include vp-1919 {
        height: 190px;
      }

      @include vp-1279 {
        height: 185px;
      }

      img {
        @include vp-1279 {
          height: 100%;
        }
        object-fit: cover;
      }
    }

    &__inner {
      padding: 18px;

      @include vp-1919 {
        padding: 16px;
      }
    }

    .title {
      max-width: 356px;
      margin-bottom: 13px;

      color: $color-default-black;

      @include vp-1919 {
        margin-bottom: 45px;
      }

      @include vp-767 {
        margin-bottom: 29px;
      }
    }

    &__info {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      margin-bottom: 15px;

      font-weight: 600;
      font-size: 12px;
      line-height: 15px;
      color: $color-alizarin-red;
      text-transform: uppercase;

      @include vp-1919 {
        margin-bottom: 19px;
      }

      @include vp-1279 {
        margin-bottom: 5px;
      }

      svg {
        width: 12px;
        height: 17px;
        margin-right: 5px;
      }
    }

    &__content-wrapper {
      display: flex;
      justify-content: space-between;

      .link {
        svg {
          margin-left: 5px;
        }
      }
    }

    &__date {
      font-weight: 700;
      font-size: 16px;
      line-height: 20px;
      color: $color-dark-gray;

      @include vp-1919 {
        font-size: 14px;
        line-height: 17px;
      }

      @include vp-1279 {
        font-size: 12px;
        line-height: 15px;
      }
    }
  }

  &__link {
    position: relative;

    display: flex;
    margin-top: -154px;
    padding-bottom: 18px;

    @include vp-1919 {
      margin-top: -147px;
    }

    @include vp-1439 {
      order: 1;
      max-width: 98%;
      margin-top: 0;
      margin-bottom: 19px;
      padding-bottom: 0;
    }

    @include vp-1279 {
      max-width: 100%;
      margin-right: 30px;
      margin-bottom: 15px;
    }

    @include vp-767 {
      order: 3;
      margin-right: 15px;
      margin-bottom: 0;
    }

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 7px;

      height: 2px;

      background-color: $color-default-white;

      pointer-events: none;

      @include vp-1439 {
        display: none;
      }
    }

    &::before {
      left: 0;

      width: 74%;

      @include vp-1919 {
        width: 76vw;
      }
    }

    &::after {
      right: 0;

      width: 15%;

      @include vp-1919 {
        width: 10.5vw;
      }
    }

    .link {
      margin-right: 16.5%;
      margin-left: auto;

      cursor: pointer;

      @include vp-1919 {
        margin-right: 12%;

        font-size: 14px;
        line-height: 17px;
      }

      @include vp-1439 {
        margin-right: 16%;
      }

      @include vp-1279 {
        margin-right: 0;
      }

      @include vp-767 {
        color: $color-default-black;

        &:hover,
        &:focus {
          color: $color-alizarin-red;
        }
      }
    }

    svg {
      margin-right: 7px;
    }
  }

  &__link:hover {
    .link::after {
      opacity: 1;
    }
  }

  .swiper-pagination-horizontal {
    bottom: 15px;

    padding-left: 15px;

    &::before,
    &::after {
      content: "";
      position: absolute;

      height: 2px;

      background-color: $color-default-black;
    }

    &::before {
      right: 0;

      width: calc(100% - 90px);
    }

    &::after {
      left: 0;

      width: calc(3px + 1%);
    }
  }

  &__pagination {
    display: none;

    @include vp-767 {
      position: absolute;

      display: inline-flex;

      .pagination__button {
        padding: 0;

        background-color: transparent;
        border: 0;
        cursor: pointer;

        &--rectangle {
          background-color: transparent;
          border: 2px solid $color-default-black;
          outline: none;
          cursor: pointer;

          transition: border $trans-600;

          &:hover,
          &:focus,
          &:active {
            border-color: $color-default-black;
            outline: none;

            transition: opacity $trans-600;
          }
        }

        &--lg {
          width: 20px;
          height: 12px;

          @include vp-1023 {
            width: 15px;
            height: 8px;

            border-width: 1px;
          }

          &:not(:first-child) {
            margin-left: 10px;
          }
        }

        &--active {
          background-color: $color-default-black;
          cursor: auto;

          transition: background-color $trans-600;

          pointer-events: none;

          &:hover,
          &:focus,
          &:active {
            border-color: $color-default-black;

            transition: border-color $trans-600;
          }
        }
      }
    }
  }

  .link {
    svg {
      width: 33px;
      height: 16px;
    }
  }
}
