.label {
  padding: 3px 12px;

  display: flex;
  justify-content: center;
  align-items: center;

  font-weight: 700;
  font-size: 14px;
  line-height: 24px;
  color: $color-default-black;
  background-color: $color-default-white;

  text-transform: uppercase;
  border: 2px solid $color-default-black;

  @include vp-1023 {
    padding: 0 10px;
    font-size: 12px;
  }
}
